<template>
  <div class="login_container">
    <form
      @submit.prevent.stop="onSubmit"
      class="input_form"
      v-if="data.stage === 0"
    >
      <div>
        <h6>  </h6>
        <h6>  .</h6>
      </div>
      <div style="margin-top: 30px">
        <InputBasic
          style="margin-top: 10px"
          outlined
          placeholder=" ."
          v-model="data.name"
          lazy-rules
          debounce="500"
        />
        <InputBasic
          style="margin-top: 10px"
          outlined
          placeholder=" . (- 6  )"
          v-model="data.birth"
          lazy-rules
          debounce="500"
          type="number"
        />
        <InputBasic
          style="margin-top: 10px"
          outlined
          placeholder=" .(-  )"
          v-model="data.phone"
          lazy-rules
          debounce="500"
        />
        <div class="fail_to_validate" v-if="failToValidate">
          {{ validationText }}
        </div>
        <ButtonBasic
          :clicked="onSubmit"
          title=""
          flat
          style="
            width: 100%;
            margin-top: 10px;
            height: 40px;
            background-color: #8e43f0;
            color: white;
          "
        />
      </div>
    </form>
    <div v-if="data.stage === 1" class="input_form">
      <h6>{{ data.name }}   .</h6>
      <div style="margin-top: 20px">
        :
        <span style="color: red; font-weight: 600">{{ data.userId }}</span>
      </div>
      <div style="margin-top: 20px">
        <ButtonBasic
          title=""
          :clicked="() => router.push('/login')"
          style="width: 100%; background-color: #8e43f0; color: white"
          flat
        />
        <ButtonBasic
          style="
            width: 100%;
            margin-top: 10px;
            background-color: #8e43f0;
            color: white;
          "
          title=" "
          flat
          :clicked="() => router.push('/forgotpassword')"
        />
      </div>
    </div>
  </div>
</template>

<script setup name="ForgotID">
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import InputBasic from "@/components/atom/input/InputBasic.vue";
import { reactive, watch, ref } from "vue";
import { useRouter } from "vue-router";
import AUTH_API from "@/api/modules/auth";
import { NUMBER } from "@/helpers/utils";

const { findid } = AUTH_API();
const router = useRouter();
const failToValidate = ref(false);
const validationText = ref("");
const data = reactive({
  name: "",
  birth: "",
  phone: "",
  stage: 0,
  userId: "",
});

const validator = (name, birth, phone) => {
  if (name === "" && birth === "" && phone === "") {
    failToValidate.value = true;
    validationText.value = "// .";
    return false;
  } else if (name === "" && birth === "" && phone !== "") {
    failToValidate.value = true;
    validationText.value = "/ .";
    return false;
  } else if (name === "" && birth !== "" && phone === "") {
    failToValidate.value = true;
    validationText.value = "/ .";
    return false;
  } else if (name !== "" && birth === "" && phone === "") {
    failToValidate.value = true;
    validationText.value = "/ .";
    return false;
  } else if (name === "" && birth !== "" && phone !== "") {
    failToValidate.value = true;
    validationText.value = " .";
    return false;
  } else if (name !== "" && birth === "" && phone !== "") {
    failToValidate.value = true;
    validationText.value = " .";
    return false;
  } else if (birth.length !== 6 && !NUMBER.test(birth)) {
    failToValidate.value = true;
    validationText.value = " '-' 6 .";
    return;
  } else if (name !== "" && birth !== "" && phone === "") {
    failToValidate.value = true;
    validationText.value = " .";
    return false;
  } else if (phone.length > 11 || phone.lenght < 10) {
    failToValidate.value = true;
    validationText.value = " '-'   .";
    return;
  } else if (!NUMBER.test(phone)) {
    failToValidate.value = true;
    validationText.value = " '-'   .";
    return;
  } else {
    failToValidate.value = false;
    validationText.value = "";
    return true;
  }
};

watch(data, (val) => {
  console.log(val);
});

const onSubmit = async () => {
  const result = validator(data.name, data.birth, data.phone);

  if (!result) {
    return;
  } else {
    const response = (userid, statusCode) => {
      data.userId = userid;
      data.stage = 1;
    };

    await findid(
      {
        userName: data.name.trim(),
        birthDt: data.birth.trim(),
        mobile: data.phone.trim(),
      },
      response,
    );
  }
};
</script>

<style lang="scss" scoped>
.login_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background: linear-gradient(180deg, $gray_100 50%, #ffffff 50%);
}

.input_form {
  width: 500px;
  margin-top: 30px;
  padding: 20px;
  background-color: $white;
  box-shadow: 0px 30px 100px rgba(68, 68, 79, 0.13);
}

.fail_to_validate {
  padding-top: 10px;
  font-size: 12px;
  color: $red;
}
</style>
