<template>
  <div :style="style">
    <q-select
      square
      outlined
      v-model="selectValue"
      :options="addedOptions"
      dense
      style="width: 100%"
      v-bind="attrs"
    />
  </div>
</template>

<script setup>
import { ref, defineProps, defineEmits, watch, useAttrs, onMounted } from "vue";
const attrs = useAttrs();

const props = defineProps({
  value: { type: String, required: false },
  options: { type: Array, required: false, default: [] },
  style: { type: Object, required: false },
  all: { type: Boolean, require: false, default: false },
});

const { options, all } = props;

const emit = defineEmits(["update:value"]);

const selectValue = ref(props.value);
const addedOptions = ref([]);

const changeOptions = (data) => {
  addedOptions.value = [];
  let arr = [...data];
  if (all) {
    arr.unshift({ value: "", label: "" });
  } else {
    arr.unshift({ value: "", label: "" });
  }
  addedOptions.value = arr;
  selectValue.value = addedOptions.value.find((v) => v.value === props.value);
};

watch(selectValue, (e) => {
  emit("update:value", e.value);
});

watch(
  () => props.value,
  (newVal) => {
    if (newVal === "") {
      selectValue.value = { value: "", label: "" };
    }
  },
);

watch(
  () => [...props.options],
  (e) => {
    changeOptions(e);
  },
);

onMounted(() => {
  const arr = options ? [...options] : [];
  changeOptions(arr);
});
</script>

<style></style>
