<template>
  <div>
    <div class="title">
      <h6>FAQ </h6>
      <span style="color: red">*  </span>
    </div>
    <div class="divider" />
    <div class="row_container">
      <span class="sub_title"><span style="color: red">*</span></span>
      <div style="display: flex; flex-direction: column; width: 90%">
        <Input-basic
          style="padding: 5px"
          placeholder=" "
          v-model="titlevalue"
        />
        <div style="text-align: right; padding-right: 5px">
          {{ titlevalue.length }}/100
        </div>
      </div>
    </div>
    <div class="divider" />
    <div class="row_container">
      <span class="sub_title"><span style="color: red">*</span></span>
      <radio-box
        v-model="type"
        :options="[
          { label: '', value: '' },
          { label: 'LMS', value: 'LMS' },
        ]"
        inline
        checked-icon="task_alt"
        unchecked-icon="panorama_fish_eye"
        val="line"
      />
    </div>
    <div class="divider" />
    <div class="row_container">
      <span class="sub_title"
        >FAQ <span style="color: red">*</span></span
      >
      <select-box v-model="category" :options="options" style="width: 200px" />
    </div>
    <div class="divider" />
    <div class="row_container">
      <span class="sub_title"><span style="color: red">*</span></span>
      <div style="display: flex; flex-direction: column; width: 90%">
        <textarea
          v-model="contentvalue"
          placeholder=" "
          class="content_input"
        />
        <div style="text-align: right; margin-top: 5px">
          {{ contentvalue.length }}/1000
        </div>
      </div>
    </div>
    <div class="divider" />
    <div class="bottom_container">
      <ButtonBasic
        title=""
        @click="router.push('/service/faq')"
        color="standard"
        textColor="black"
      />
      <ButtonBasic
        title=""
        @click="openRegistConfirmDialog"
        style="padding: 0 20px"
      />
    </div>
  </div>
</template>

<script setup name="FAQRegist">
import SelectBox from "@/components/molecule/select/SelectBox.vue";
import RadioBox from "@/components/molecule/Radio/RadioBox.vue";
import InputBasic from "@/components/atom/input/InputBasic.vue";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import { ref, watch } from "vue";
import { useRouter } from "vue-router";
import { DialogStore } from "@/store/modules/dialog";

const router = useRouter();
const store = DialogStore();
const titlevalue = ref("");
const contentvalue = ref("");
const category = ref("");
const type = ref("");
const options = ["Google", "Facebook", "Twitter", "Apple", "Oracle"];

watch(titlevalue, (e) => {
  if (titlevalue.value.length > 100) {
    alert("100  ");
    titlevalue.value = titlevalue.value.slice(0, 99);
    return;
  }
});

watch(type, (e) => {
  console.log(e.value);
});

const checkConfirm = (value) => {
  if (value) {
    let data = {
      channel: type.value.value,
      title: titlevalue.value,
      content: contentvalue.value,
      category: category.value,
    };
    console.log("submit:", data);
    router.push("/service/faq");
  }
};

const openRegistConfirmDialog = () => {
  store.openConfirmDialog({
    message: "  ?",
    cancel: "",
    ok: "",
    callback: checkConfirm,
  });
};
</script>

<style lang="scss" scoped>
.title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.divider {
  margin: 5px 0px;
  width: 100%;
  height: 1px;
  background-color: #99999930;
}
.row_container {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 20px 0px 10px;
}

.sub_title {
  padding: 5px;
  width: 120px;
}
.content_input {
  height: 300px;
  padding: 5px;
}

.bottom_container {
  width: 100%;
  height: 80px;
  padding: 20px 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
</style>
