<template>
  <div class="login_container">
    <div class="login_form">
      <div class="title">
        <h5 style="color: #ddd">  </h5>
      </div>
      <form
        @submit.prevent.stop="onSubmit"
        @reset.prevent.stop="onReset"
        class="input_form"
      >
        <h4 class="logo"></h4>
        <div class="login_info">  .</div>
        <div class="input_email">
          <InputBasic
            ref="idRef"
            outlined
            placeholder=""
            v-model="id"
            lazy-rules
            debounce="500"
          />
        </div>
        <div class="input_password">
          <InputBasic
            ref="pwRef"
            outlined
            placeholder=""
            v-model="pw"
            lazy-rules
            debounce="500"
            type="password"
          />
        </div>
        <div
          style="
            display: flex;
            justify-content: space-between;
            align-items: center;
          "
        >
          <div
            style="
              display: flex;
              flex-direction: row;
              align-items: center;              
              font-size: 12px;
              color: #8e8e8e;
            "
          >
            <q-checkbox v-model="saveid" size="xs" > </q-checkbox>
          </div>
          <div style="display: flex; flex-direction: row">
            <div
              v-on:click="router.push('/forgotid')"
              style="
                font-size: 12px;
                color: #8e8e8e;
                text-decoration: underline;
                cursor: pointer;
              "
            >
              
            </div>
            <div
              v-on:click="router.push('/forgotpassword')"
              style="
                margin-left: 10px;
                color: #8e8e8e;
                font-size: 12px;
                text-decoration: underline;
                cursor: pointer;
              "
            >
               
            </div>
          </div>
        </div>
        <div class="fail_to_validate" v-if="failToValidate">
          {{ validationText }}
        </div>
        <div style="margin-top: 10px">
          <ButtonBasic
            v-if="activebutton"
            label=""
            type="submit"
            flat
            style="width: 100%; background-color: #8E43F0; color: white;"
          />
          <ButtonBasic
            v-else
            label=""
            type="submit"
            flat
            style="width: 100%; background-color: #ededed; color: #8e8e8e"
          />
        </div>
      </form>
    </div>
    <div style="margin-top: 100px; color: #8e8e8e; text-align: center;">
      Copyright  DAEKYO. All Right Reserved.
    </div>
    <div v-if="isShowModal" class="change_current_pw">
      <ChangePassword :isShowModal="isShowModal" @onHideModal="onHideModal" :id="id"/>
    </div>
  </div>
</template>

<script setup name="login">
import { GlobalStore } from "@/store";
import { onMounted } from "vue";
import { useRouter } from "vue-router";
import { ref, watch } from "vue";
import AUTH_API from "@/api/modules/auth"
import InputBasic from "@/components/atom/input/InputBasic.vue";
import ChangePassword from '@/views/login/components/ChangePassword.vue'
import { setCookie, getCookie } from "./saveId";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import { DialogStore } from "@/store/modules/dialog";

const globalStore = GlobalStore();
const dialog = DialogStore()
const router = useRouter();

const {login, changepassword} = AUTH_API()

const id = ref(null);
const idRef = ref(null);

const pw = ref(null);
const pwRef = ref(null);

const saveid = ref(false);

const failToValidate = ref(false);
const activebutton = ref(false);
const validationText = ref("");
const isShowModal = ref(false)

// cookie     -  id value 
onMounted(() => {
  let savedId = getCookie("c_userid");
  if (savedId == null || typeof savedId == "undefined" || savedId === "") {
    id.value = null;
    saveid.value = false;
  } else {
    id.value = savedId;
    saveid.value = true;
  }
});

//   
watch(saveid, (val) => {
  if (val == true) {
    setCookie("c_userid", id.value, "100");
  } else {
    setCookie("c_userid", "", "100");
  }
});

// ,      watch
watch([id, pw], (val) => {
  if (val[0]?.length > 0 && val[1]?.length > 0) {
    activebutton.value = true;
  } else {
    activebutton.value = false;
  }
});

// ,   
const validator = (id, pw) => {
  if ((id === null || id === "") && (pw === null || pw === "")) {
    failToValidate.value = true;
    validationText.value = "/ .";
    return false;
  } else if ((id === null || id === "") && (pw !== "" || pw !== null)) {
    failToValidate.value = true;
    validationText.value = " .";
    return false;
  } else if ((id !== null || id !== "") && (pw === null || pw === "")) {
    failToValidate.value = true;
    validationText.value = " .";
    return false;
  } else {
    failToValidate.value = false;
    validationText.value = "";
    return true;
  }
};

//    submit
const onSubmit = async () => {
  const result = validator(id.value, pw.value);
  if (!result) {
    return;
  } else {
    const loginHandler = (data, statusCode) => {
      if (statusCode === '823' || statusCode === '806') {
        dialog.openAlertDialog({
          message: '   .',
          buttonText: ''
        })
        return;
      } else if (statusCode === "825") {
        router.push({ name: 'Initialize', state: {id: id.value} }); // id 
      } else if (statusCode === "826") {
        dialog.openAlertDialog({
          message:
            "   .       .   .",
          buttonText: "",
        });
        return;
      } else if (statusCode === '827') {
        isShowModal.value = true
        pw.value = ""
        return;
      } else if (statusCode === '200') {
        router.push("/");
      }
    };
    await login({ loginId: id.value, password: pw.value }, loginHandler);
  }
};

// form reset
const onReset = () => {
  id.value = null;
  pw.value = null;

  idRef.value.resetValidation();
  pwRef.value.resetValidation();
};

// 90 ....   modal 
const onHideModal = () => {
  isShowModal.value = false
}

//  token   dashboard 
onMounted(() => {
  if (globalStore.token !== "") {
    router.push("/");
  }
});
</script>

<style scoped lang="scss">
@import "./index.scss";

.login_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background: linear-gradient(180deg, $gray_100 50%, #ffffff 50%);
}

.fail_to_validate {
  padding: 5px 0px;
  font-size: 12px;
  color: $red;
}

.logo {
  margin-top: 20px;
  font-weight: 700;
  color: $primary;
  text-align: center;
}

.title {
  text-align: center;
}

.input_form {
  margin-top: 30px;
  padding: 20px;
  background-color: $white;
  box-shadow: 0px 30px 100px rgba(68, 68, 79, 0.13);
}

.input_email {
  margin-top: 24px;
}

.input_password {
  margin-top: 18px;
  margin-bottom: 10px;
}

.login_info {
  font-size: 12px;
  color: $gray_600;
  text-align: center;
}

.login_form {
  display: flex;
  flex-direction: column;
  height: 500px;
  width: 400px;
}

.change_current_pw {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #00000080;
}
</style>
