<template>
  <q-layout view="lHh lpR lFf">
    <Header :drawer="drawer" :name="name" @handleMenu="handleMenu" />
    <Lnb v-if="drawer" />
    <q-page-container>
      <breadcrumb-container :location="location" :name="name">
        <router-view></router-view>
      </breadcrumb-container>
    </q-page-container>
  </q-layout>
</template>

<script setup>
import Header from "@/components/layouts/header/headerLayout.vue";
import Lnb from "./lnb/Lnb.vue";
import BreadcrumbContainer from "../organism/BreadcrumbContainer.vue";
import { useRoute } from "vue-router";
import { ref, watch } from "vue";

const route = useRoute();
const location = ref(route.meta.location);
const name = ref(route.meta.name);

const drawer = ref(true);
const handleMenu = () => {
  drawer.value = !drawer.value;
};

watch(route, () => {
  location.value = route.meta.location;
  name.value = route.meta.name;
});
</script>
