<template>
  <div class="row">
    <div class="col-md-4">
      <div class="q-pa-sm">
        <q-table
          :columns="COLUMNS.AUTH_GROUP_LIST"
          row-key="name"
          flat
          bordered
        />
      </div>
    </div>
    <div class="col-md-4">
      <div class="q-pa-sm">
        <q-markup-table class="q-mb-sm" flat>
          <colgroup>
            <col width="15%" style="background-color: #99999910" />
            <col width="35%" />
          </colgroup>
          <tbody>
            <tr>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td></td>
              <td></td>
            </tr>
          </tbody>
        </q-markup-table>
        <div>
          <button-basic title="" />
        </div>
      </div>
    </div>
    <div class="col-md-4">
      <div class="q-pa-sm">
        <div v-for="item in menu_arr">
          <checkbox-basic
            type="single"
            v-model:value="group"
            :val="item.label"
            :label="item.label"
            @update:model-value="check(item.label)"
          />
          <div v-if="item.children && item.children.length > 0">
            <checkbox-basic
              class="q-ml-md"
              type="group"
              v-model:value="auth_list"
              :options="
                item.children.map((v) => {
                  return { label: v.name, value: v.name };
                })
              "
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script setup>
import CheckboxBasic from "@/components/atom/checkbox/CheckboxBasic.vue";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import { menu } from "@/constants/menu";
import COLUMNS from "@/constants/columns";
import { ref } from "vue";
const group = ref([]);
const auth_list = ref([]);
const menu_arr = menu
  .filter((v) => v.name !== "")
  .map((v) => {
    return { label: v.name, value: v.name, children: v.children };
  });

const check = (value) => {
  const index = group.value.indexOf(value);
  if (index > -1) {
    group.value.splice(index, 1);
    menu_arr.map((val) => {
      if (val.label === value && val.children && val.children.length > 0) {
        val.children.map((v) => {
          const idx = auth_list.value.indexOf(v.name);
          if (idx > -1) {
            auth_list.value.splice(idx, 1);
          }
        });
      }
    });
  } else {
    group.value.push(value);
    menu_arr.map((val) => {
      if (val.label === value && val.children && val.children.length > 0) {
        val.children.map((v) => {
          if (auth_list.value.indexOf(v.name) < 0) {
            auth_list.value.push(v.name);
          }
        });
      }
    });
  }
};
</script>
<style lang=""></style>
