<template>
  <div class="change_password_container">
    <div class="title">  </div>
    <div class="content">
      <div class="content_top_info">
           90   .
      </div>
      <div style="line-height: 1.7">
                 
              .
      </div>
      <form style="margin-top: 20px">
        <div>
          <InputBasic
            style="margin-top: 10px"
            outlined
            placeholder="  "
            v-model="data.currentpw"
            lazy-rules
            debounce="500"
            type="password"
          />
          <InputBasic
            style="margin-top: 10px"
            outlined
            placeholder=" "
            v-model="data.newpw"
            lazy-rules
            debounce="500"
            type="password"
          />
          <InputBasic
            style="margin-top: 10px"
            outlined
            placeholder="  "
            v-model="data.newpwconfirm"
            lazy-rules
            debounce="500"
            type="password"
          />
        </div>
        <div
          style="
            display: flex;
            flex-direction: row;
            justify-content: center;
            margin-top: 20px;
          "
        >
          <ButtonBasic
            :clicked="onChangeLater"
            title=" "
            style="
              width: 150px;
              margin-top: 10px;
              height: 40px;
              margin-right: 20px;
            "
          />
          <ButtonBasic
            :clicked="onSubmit"
            title=""
            style="width: 150px; margin-top: 10px; height: 40px"
          />
        </div>
      </form>
    </div>
  </div>
</template>

<script setup name="ChangePassword">
import InputBasic from "@/components/atom/input/InputBasic.vue";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import { reactive, watch, defineEmits, defineProps } from "vue";
import { PASSWORD } from "@/helpers/utils";
import router from "@/routers";
import AUTH_API from "@/api/modules/auth";
import { DialogStore } from "@/store/modules/dialog";

const emit = defineEmits(["onHideModal", {}]);
const dialog = DialogStore();
const { changepassword, changepasswordlater } = AUTH_API();

const props = defineProps({
  id: { type: String, require: true },
});

const { id } = props;

const data = reactive({
  currentpw: "",
  newpw: "",
  newpwconfirm: "",
});

const hideModal = () => {
  emit("onHideModal");
};

const onChangeLater = async () => {
  const res = await changepasswordlater();
  if (res) {
    hideModal();
    router.push("/");
  }
};

const onSubmit = async () => {
  if (data.currentpw === data.newpw) {
    dialog.openAlertDialog({
      message:
        "    .   .",
      buttonText: "",
    });
    console.log("");
  } else if (data.newpw !== data.newpwconfirm) {
    dialog.openAlertDialog({
      message: "  .   .",
      buttonText: "",
    });
  } else if (!PASSWORD.test(data.newpw)) {
    dialog.openAlertDialog({
      message:
        " 8~20 ,,  .",
      buttonText: "",
    });
  } else {
    const response = (data, statusCode) => {
      if (statusCode === "823") {
        dialog.openAlertDialog({
          message: "   .   .",
          buttonText: "",
        });
        hideModal();
        return;
      } else if (statusCode === "828") {
        dialog.openAlertDialog({
          message: "   .",
          buttonText: "",
        });
        return;
      } else if (statusCode === "200") {
        dialog.openAlertDialog({
          message: " .",
          buttonText: "",
        });
        hideModal();
      }
    };
    await changepassword(
      {
        loginId: id,
        currentPassword: data.currentpw,
        password: data.newpw,
      },
      response,
    );
  }
};
</script>

<style lang="scss" scoped>
.change_password_container {
  width: 450px;
  height: 500px;
  display: flex;
  flex-direction: column;
  background-color: white;
  border: 5px solid #ddd;
  border-radius: 8px;
}
.title {
  background-color: #ddd;
  padding: 5px;
}

.content {
  padding: 50px 26px;

  &_top_info {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 30px;
  }
}
</style>
