<template>
  <div class="appversion_container">
    <h6> </h6>
    <table class="table">
      <tbody class="table_body">
        <tr class="table_row">
          <td class="table_title">
            <span style="color: red">*</span>
          </td>
          <td class="table_data">{{ versionNumber }}</td>
        </tr>
        <tr class="table_row">
          <td class="table_title"><span style="color: red">*</span></td>
          <td class="table_data">
            <radio-box
              v-model="mobileOS"
              :options="[
                {
                  label: 'IOS',
                  value: 'IOS',
                },
                {
                  label: 'ANDROID',
                  value: 'ANDROID',
                },
              ]"
              inline
            />
          </td>
        </tr>
        <tr class="table_row">
          <td class="table_title"><span style="color: red">*</span></td>
          <td class="table_data">
            <input-basic v-model="appversion" placeholder="App Version" />
          </td>
        </tr>
        <tr class="table_row">
          <td class="table_title">
            <span style="color: red">*</span>
          </td>
          <td class="table_data">
            <radio-box
              v-model="udtType"
              :options="[
                {
                  label: 'MAJOR',
                  value: 'MAJOR',
                },
                {
                  label: 'MINOR',
                  value: 'MINOR',
                },
              ]"
              inline
            />
          </td>
        </tr>
        <tr class="table_row">
          <td class="table_title">
            <span style="color: red">*</span>
          </td>
          <td class="table_data"><Calendar :date="dateOfRegist" /></td>
        </tr>
        <tr class="table_row">
          <td class="table_title"><span style="color: red">*</span></td>
          <td class="table_data"><Calendar :date="dateOfDeploy" /></td>
        </tr>
        <tr class="table_row">
          <td class="table_title">URL<span style="color: red">*</span></td>
          <td class="table_data"><input-basic v-model="marketURL" /></td>
        </tr>
        <tr class="table_row">
          <td class="table_title">
            <span style="color: red">*</span>
          </td>
          <td class="table_data"><input-basic v-model="udtTitle" /></td>
        </tr>
        <tr class="table_row">
          <td class="table_title">
            <span style="color: red">*</span>
          </td>
          <td class="table_data">
            <div style="height: 120px; padding-top: 5px">
              <input-basic
                type="textarea"
                :input-style="{ resize: 'none' }"
                rows="5"
                v-model="message"
              />
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="bottom_container">
      <ButtonBasic
        title=""
        @click="moveToList"
        color="standard"
        textColor="black"
      />
      <ButtonBasic
        title=""
        @click="openRegistConfirmDialog"
        style="padding: 0 20px"
      />
    </div>
  </div>
</template>

<script setup name="VersionDetail">
import InputBasic from "@/components/atom/input/InputBasic.vue";
import RadioBox from "@/components/molecule/Radio/RadioBox.vue";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import Calendar from "@/components/atom/calendar/Calendar.vue";
import { ref, onMounted } from "vue";
import { useRouter } from "vue-router";
import { DialogStore } from "@/store/modules/dialog";

const store = DialogStore();
const router = useRouter();

const mobileOS = ref("");
const versionNumber = ref("");
const appversion = ref("");
const udtType = ref("");
const dateOfRegist = ref(history.state.data.date_of_registration);
const dateOfDeploy = ref(history.state.data.date_of_deployment);
const marketURL = ref("");
const udtTitle = ref("");
const message = ref("");

const submit = (value) => {
  if (value) {
    console.log("submit");
    router.push("/service/version");
  }
};

const openRegistConfirmDialog = () => {
  store.openConfirmDialog({
    message: "  ?",
    cancel: "",
    ok: "",
    callback: submit,
  });
};

const moveToList = () => {
  router.push("/service/version");
};

const changeMobileOS = (value) => {
  mobileOS.value = value;
};

const changeUdtType = (value) => {
  udtType.value = value;
};

onMounted(() => {
  if (history.state.data) {
    mobileOS.value = {
      label: history.state.data.mobile_OS,
      value: history.state.data.mobile_OS,
    };
    versionNumber.value = history.state.data.version_number;
    appversion.value = history.state.data.version;
    udtType.value = {
      label: history.state.data.update_category,
      value: history.state.data.update_category,
    };
    dateOfRegist.value = history.state.data.date_of_registration;
    dateOfDeploy.value = history.state.data.date_of_deployment;
    marketURL.value = history.state.data.market_URL;
    udtTitle.value = history.state.data.update_title;
  }
});
</script>

<style lang="scss" scoped>
.table {
  width: 100%;
  margin-top: 20px;

  &_body {
    width: 100%;
  }

  &_row {
    height: 50px;
    border-top: 1px solid #99999940;
    border-bottom: 1px solid #99999940;
  }

  &_title {
    width: 20%;
    background-color: #99999910;
    padding-left: 20px;
  }

  &_data {
    padding-left: 20px;
  }
}

.appversion_container {
  width: 100%;
  height: 100%;
}
</style>
