<template>
  <div class="calendar_container">
    <q-field square outlined v-model="startdate" dense>
      <template v-slot:append>
        <q-icon
          name="expand_more"
          class="cursor-pointer"
          style="color: #616161"
        >
          <q-popup-proxy
            transition-show="jump-down"
            transition-hide="jump-up"
            :offset="[130, 10]"
          >
            <q-date v-model="startdate" mask="YYYY-MM-DD" minimal>
              <div class="row items-center justify-end">
                <q-btn v-close-popup label="Close" color="primary" flat />
              </div>
            </q-date>
          </q-popup-proxy>
        </q-icon>
      </template>
      <template v-slot:control>
        <div class="self-center full-width no-outline q-pr-md" tabindex="0">
          {{ startdate }}
        </div>
      </template>
    </q-field>
    <div style="margin: 0 20px">~</div>
    <q-field square outlined v-model="enddate" dense>
      <template v-slot:append>
        <q-icon
          name="expand_more"
          class="cursor-pointer"
          style="color: #616161"
        >
          <q-popup-proxy
            transition-show="jump-down"
            transition-hide="jump-up"
            :offset="[130, 10]"
          >
            <q-date v-model="enddate" mask="YYYY-MM-DD" minimal>
              <div class="row items-center justify-end">
                <q-btn v-close-popup label="Close" color="primary" flat />
              </div>
            </q-date>
          </q-popup-proxy>
        </q-icon>
      </template>
      <template v-slot:control>
        <div class="self-center full-width no-outline q-pr-md" tabindex="0">
          {{ endDate }}
        </div>
      </template>
    </q-field>
    <q-option-group
      class="q-ml-sm"
      v-model="setdate"
      :options="options"
      inline
      @update:model-value="updateDate"
    >
      <template v-slot:label="opt">
        <div class="row items-center">
          <span style="color: #6f767e">{{ opt.label }}</span>
        </div>
      </template>
    </q-option-group>
  </div>
</template>

<script setup name="datepicker">
import { defineProps, watch, defineEmits, ref } from "vue";
import { useQuasar } from "quasar";
import moment from "moment";

const $q = useQuasar();
const emit = defineEmits(
  ["changeDate", { type: String, date: String }],
  ["update:startDate"],
  ["update:endDate"],
);
const props = defineProps({
  startDate: { type: String, required: true, default: "0000-00-00" },
  endDate: { type: String, required: true, default: "0000-00-00" },
});
const startdate = ref(props.startDate);
const enddate = ref(props.endDate);
const setdate = ref(0);
const options = [
  { value: 0, label: "" },
  { value: 1, label: "7" },
  { value: 2, label: "1" },
  { value: 3, label: "6" },
];

const updateDate = (val) => {
  const today = moment().format("YYYY-MM-DD");
  const nextweek = moment().add(7, "d").format("YYYY-MM-DD");
  const nextmonth = moment().add(1, "M").format("YYYY-MM-DD");
  const sixmonth = moment().add(6, "M").format("YYYY-MM-DD");
  switch (val) {
    case 0:
      emit("changeDate", { type: "start", date: today });
      emit("changeDate", { type: "end", date: today });
      emit("update:startDate", today);
      emit("update:endDate", today);
      break;
    case 1:
      emit("changeDate", { type: "start", date: today });
      emit("changeDate", { type: "end", date: nextweek });
      emit("update:startDate", today);
      emit("update:endDate", nextweek);
      break;
    case 2:
      emit("changeDate", { type: "start", date: today });
      emit("changeDate", { type: "end", date: nextmonth });
      emit("update:startDate", today);
      emit("update:endDate", nextmonth);
      break;
    case 3:
      emit("changeDate", { type: "start", date: today });
      emit("changeDate", { type: "end", date: sixmonth });
      emit("update:startDate", today);
      emit("update:endDate", sixmonth);
      break;
  }
};

const handleSelect = {
  validate: function (type, pickeddate) {
    if (type === "start") {
      if (enddate.value !== "0000-00-00") {
        if (pickeddate > enddate.value) return false;
        else return true;
      } else {
        return true;
      }
    } else {
      if (pickeddate < startdate.value) return false;
      else return true;
    }
  },
  failToValidate: function (text) {
    $q.notify({
      color: "red-4",
      textColor: "white",
      icon: "cloud_done",
      message: text,
    });
  },
};

watch(props, () => {
  startdate.value = props.startDate;
  enddate.value = props.endDate;
});

watch(startdate, (e) => {
  const isValidate = handleSelect.validate("start", e);

  if (isValidate === false) {
    handleSelect.failToValidate(
      "the start time must be ealier than the end time",
    );
    let newStart = moment(enddate.value)
      .subtract("1", "d")
      .format("YYYY-MM-DD");
    startdate.value = newStart;

    let arg = { type: "start", date: newStart };
    emit("changeDate", arg);
    emit("update:startDate", newStart);
    return;
  } else {
    let arg = { type: "start", date: e };
    emit("changeDate", arg);
    emit("update:startDate", e);
  }
});

watch(enddate, (e) => {
  const isValidate = handleSelect.validate("end", e);

  if (isValidate === false) {
    handleSelect.failToValidate(
      "the end time must be later than the start time",
    );
    let newEnd = moment(startdate.value).add("1", "d").format("YYYY-MM-DD");
    enddate.value = newEnd;
    let arg = { type: "end", date: newEnd };
    emit("changeDate", arg);
    emit("update:endDate", newEnd);
    return;
  } else {
    let arg = { type: "end", date: e };
    emit("changeDate", arg);
    emit("update:endDate", e);
  }
});
</script>

<style lang="scss" scoped>
.calendar_container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.date {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  width: 200px;
  height: 40px;
  border: 1px solid #ddd;
}

.q-input-target,
.q-input-shadow {
  color: #ddd;
}
// .calendar {
//     position: absolute;
//     z-index: 999;
//     top: 40px;
// }
</style>
