<template>
  <div v-if="showHeader">
    <div class="text-h6">{{ title }}</div>
    <div style="display: flex; align-items: center; margin-bottom: 0.5rem">
      <div style="flex: 1">
         <span style="font-weight: bold">{{ total }}</span
        >   .
      </div>
      <!-- 2 22()   selectbox  -->
      <!-- <div style="display: flex; justify-content: flex-end">
        <q-select
          dense
          :options-dense="true"
          outlined
          v-model="model"
          :options="options"
        />
      </div> -->
      <slot></slot>
    </div>
  </div>
  <div>
    <q-table
      flat
      bordered
      class="my-sticky-dynamic"
      v-bind="attrs"
      :rows="rowsData"
      :columns="props.columns"
      row-key="name"
      :hide-bottom="rows.length !== 0"
      @row-click="onRowClick"
      virtual-scroll
      :virtual-scroll-item-size="48"
      :virtual-scroll-sticky-size-start="48"
      :pagination="pagination"
      :rows-per-page-options="[0]"
      @virtual-scroll="onScroll"
      no-data-label=" ."
      no-results-label=" ."
    >
      <!-- 2 10    -->
      <template v-slot:body-cell-action="props">
        <q-td :auto-width="true" :props="props">
          <q-btn
            style="
              background-color: white;
              color: black;
              border-radius: 8px;
              border: 1px solid #8e8e8e;
            "
            class="q-px-md"
            label=""
            no-caps
            flat
            dense
            @click="props.col.onclick(props.row, props.rowIndex)"
          />
        </q-td>
      </template>
      <!-- 2 15     -->
      <template v-slot:body-cell-link="props">
        <q-td :props="props">
          <span
            style="text-decoration: underline"
            @click="props.col.onclick(props.row, props.rowIndex)"
            >{{ props.row.link }}</span
          >
        </q-td>
      </template>
      <template v-slot:no-data="{ message }">
        <div class="full-width text-center q-gutter-sm q-py-xl text-grey-6">
          <q-icon size="5em" name="r_warning_amber" />
          <div style="font-size: 16px">{{ message }}</div>
        </div>
      </template>
    </q-table>
  </div>
  <!-- <div class="q-pa-md flex flex-center">
    <q-pagination
      v-model="page"
      :max="pagesNumber"
      direction-links
      boundary-links
      color="grey"
      active-color="black"
      active-design="flat"
      size="0.8rem"
      icon-first="keyboard_double_arrow_left"
      icon-last="keyboard_double_arrow_right"
    />
  </div> -->
</template>

<script setup name="Grid">
import { defineProps, useAttrs, ref, computed, watch, nextTick } from "vue";
const attrs = useAttrs();
const props = defineProps({
  title: { type: String },
  rows: { type: Object, require: true, default: [] },
  total: { type: Number, default: 0 },
  columns: { type: Object, require: true },
  options: {
    type: Object,
    require: false,
    default: [
      {
        value: 10,
        label: "10",
      },
      {
        value: 20,
        label: "20",
      },
      {
        value: 30,
        label: "30",
      },
    ],
  },
  onchange: { type: Function, require: false, default: () => {} },
  showHeader: { type: Boolean, require: false, default: true },
  onRowClick: { type: Function, require: false, default: () => {} },
});
const model = ref(props.options[0]);
// const page = ref(1);
// const pagination = ref({
//   sortBy: "desc",
//   descending: false,
//   page: page,
//   rowsPerPage: model.value.value,
// });

const { rows } = props;

const pageSize = 10;
const pagination = { rowsPerPage: 0 };

const nextPage = ref(2);
const loading = ref(false);

const rowsData = computed(() =>
  rows?.slice(0, pageSize * (nextPage.value - 1)),
);

const lastPage = Math.ceil(props.rows?.length / pageSize);

const onScroll = ({ to, ref }) => {
  const lastIndex = rowsData.value.length - 1;

  if (
    loading.value !== true &&
    nextPage.value <= lastPage &&
    to === lastIndex
  ) {
    loading.value = true;
    nextPage.value++;

    setTimeout(() => {
      nextPage.value++;
      nextTick(() => {
        ref.refresh();
        loading.value = false;
      });
    }, 500);
  }
};

// const pagesNumber = computed(() => {
//   if (rows && rows.length > 0) {
//     return Math.ceil(rows.length / pagination.value.rowsPerPage);
//   } else {
//     return 1;
//   }
// });
// watch(model, () => {
//   pagination.value.rowsPerPage = model.value.value;
//   onchange(pagination.value.page, model.value.value);
// });
// watch(page, () => {
//   pagination.value.page = page;
//   onchange(pagination.value.page, model.value.value);
// });
</script>

<style lang="scss">
.my-sticky-dynamic {
  max-height: 600px;

  .q-table__top,
  .q-table__bottom,
  thead tr:first-child th {
    background-color: #fff;
  }

  thead tr th {
    position: sticky;
    z-index: 1;
  }

  thead tr:last-child th {
    top: 48px;
  }

  thead tr:first-child th {
    top: 0;
  }
}
</style>
