<template>
  <div style="display: flex">
    <template v-if="type === 'single'">
      <q-checkbox v-bind="attrs" v-model="value" />
    </template>
    <template v-if="type === 'group'">
      <template v-if="all">
        <q-checkbox
          label=""
          v-model="all_check"
          style="margin-right: 0.5rem"
          @update:model-value="checkAll"
        />
      </template>
      <q-option-group
        v-bind="attrs"
        v-model="value"
        type="checkbox"
        @update:model-value="updateValue"
      />
    </template>
  </div>
</template>
<script setup>
import { ref, defineEmits, useAttrs, defineProps, watch } from "vue";
const attrs = useAttrs();
const all_check = ref(false);
const emit = defineEmits(["update:value"]);

const props = defineProps({
  value: { type: Array, require: true },
  type: { type: String, require: false, default: "single" }, //single, group
  all: { type: Boolean, require: false, default: false },
});
const value = ref(props.value);

const { type, all } = props;

const updateValue = (newValue) => {
  emit("update:value", newValue);
};

const checkAll = (isCheck) => {
  const arr = attrs.options.map((v) => v.value);
  if (isCheck) {
    value.value = arr;
    emit("update:value", arr);
  } else {
    value.value = [];
    emit("update:value", []);
  }
};

watch(props, () => {
  value.value = props.value;
});

watch(value, () => {
  if (type === "group") {
    if (value.value.length === attrs.options.length) {
      if (!all_check.value) {
        all_check.value = true;
      }
    } else {
      if (all_check.value) {
        all_check.value = false;
      }
    }
  }
});
</script>
<style></style>
