<template>
  <div
    class="login_container"
    style="display: flex; align-items: center; justify-content: center"
  >
    <form
      class="input_form"
      v-if="data.stage === 0"
      @submit.prevent.stop="onCheckUser"
    >
      <h6>  </h6>
      <h6>  .</h6>
      <input-basic
        placeholder=" ."
        style="margin-top: 20px"
        v-model="data.name"
        lazy-rules
        debounce="500"
      />
      <InputBasic
        style="margin-top: 10px"
        outlined
        placeholder=" .(-  )"
        v-model="data.phone"
        lazy-rules
        debounce="500"
        type="number"
      />
      <div class="fail_to_validate" v-if="failToValidate">
        {{ validationText }}
      </div>
      <ButtonBasic
        :clicked="onCheckUser"
        title=""
        flat
        style="
          margin-top: 10px;
          height: 40px;
          width: 100%;
          background-color: #8e43f0;
          color: white;
        "
      />
    </form>
    <form
      class="input_form"
      v-if="data.stage === 1"
      @submit.prevent.stop="onChangePassword"
    >
      <h6> .</h6>
      <div style="margin-top: 20px">
        :
        <span style="font-weight: 600; color: red">{{ data.maskLoginId }}</span>
      </div>
      <div>
        <InputBasic
          style="margin-top: 10px"
          outlined
          placeholder="  ."
          v-model="data.newpw"
          lazy-rules
          debounce="500"
          type="password"
        />
        <InputBasic
          style="margin-top: 10px"
          outlined
          placeholder="  ."
          v-model="data.newpwconfirm"
          lazy-rules
          debounce="500"
          type="password"
        />
      </div>
      <div class="fail_to_validate" v-if="checkValidation">
        {{ validText }}
      </div>
      <ButtonBasic
        :clicked="onChangePassword"
        title=""
        flat
        style="
          width: 100%;
          margin-top: 10px;
          height: 40px;
          background-color: #8e43f0;
          color: white;
        "
      />
    </form>
  </div>
</template>

<script setup>
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import InputBasic from "@/components/atom/input/InputBasic.vue";
import { useRouter } from "vue-router";
import { reactive, ref } from "vue";
import AUTH_API from "@/api/modules/auth";
import { NUMBER, PASSWORD } from "@/helpers/utils";
import { DialogStore } from "@/store/modules/dialog";

const router = useRouter();
const { checkuser, changepassword } = AUTH_API();
const dialog = DialogStore();

const stage = ref(history.state.stage);
const failToValidate = ref(false);
const validationText = ref("");

const checkValidation = ref(false);
const validText = ref("");

const data = reactive({
  name: "",
  phone: "",
  stage: stage.value === 1 ? 1 : 0,
  userid: "",
  maskLoginId: "",
  newpw: "",
  newpwconfirm: "",
});

const onCheckUser = async () => {
  console.log(NUMBER.test(data.phone));
  if (data.name === "" && data.phone === "") {
    failToValidate.value = true;
    validationText.value = "/ .";
    return;
  } else if (data.name === "" && data.phone !== "") {
    failToValidate.value = true;
    validationText.value = " .";
    return;
  } else if (!NUMBER.test(data.phone)) {
    failToValidate.value = true;
    validationText.value = " '-'   .";
    return;
  } else if (data.name !== "" && data.phone === "") {
    failToValidate.value = true;
    validationText.value = " .";
    return;
  } else {
    const response = (res, statusCode) => {
      data.userid = res.loginId;
      data.maskLoginId = res.maskLoginId;
      console.log(res);
      data.stage = 1;
    };
    await checkuser(
      { userName: data.name.trim(), mobile: data.phone.trim() },
      response,
    );
  }
};

const onChangePassword = async () => {
  if (data.newpw === "" || data.newpwconfirm === "") {
    checkValidation.value = true;
    validText.value = " .";
    return;
  } else if (data.newpw !== data.newpwconfirm) {
    dialog.openAlertDialog({
      message: "  .   .",
      buttonText: "",
    });
    return;
  } else if (!PASSWORD.test(data.newpw)) {
    dialog.openAlertDialog({
      message:
        " 8~20 ,,  .",
      buttonText: "",
    });
  } else {
    const response = (data, statusCode) => {
      if (statusCode === "823") {
        dialog.openAlertDialog({
          message: "   .   .",
          buttonText: "",
        });
        data.stage = 0;
        return;
      } else if (statusCode === "829") {
        dialog.openAlertDialog({
          message:
            "    .   .",
          buttonText: "",
        });
        return;
      } else if (statusCode === "200") {
        dialog.openAlertDialog({
          message: " .",
          buttonText: "",
        });
        router.push("/login");
      }
    };

    await changepassword(
      {
        loginId: data.userid,
        password: data.newpw,
      },
      response,
    );
  }
};
</script>

<style lang="scss" scoped>
.login_container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, $gray_100 50%, #ffffff 50%);
}

.input_form {
  width: 500px;
  margin-top: 30px;
  background-color: $white;
  box-shadow: 0px 30px 100px rgba(68, 68, 79, 0.13);
  padding: 20px;
}

.fail_to_validate {
  padding-top: 10px;
  font-size: 12px;
  color: $red;
}
</style>
