<template>
  <section class="alert__wrap">
    <div class="alert">
      <div class="alert__message" v-html="alertOption.message || ''"></div>
      <footer class="btn__wrap">
        <button
          type="button"
          @keydown.enter="closeAlert($event)"
          @click="closeAlert($event)"
          v-html="alertOption.buttonText || ''"
        ></button>
      </footer>
    </div>
  </section>
</template>

<script>
import { computed } from "vue";
import { DialogStore } from "@/store/modules/dialog";

export default {
  name: "alert-dialog",
  setup() {
    const store = DialogStore();
    const alertOption = computed(() => store.alertOption);

    const closeAlert = () => {
      console.log("close alert");
      store.closeAlertDialog();
      // store.commit('dialogStore/closeAlertDialog');
    };

    return {
      alertOption,
      closeAlert,
    };
  },
};
</script>

<style scoped lang="scss">
.alert {
  overflow: hidden;
  width: 340px;
  border-radius: 8px;
  box-sizing: border-box;
  background: $white;

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2000;
    background-color: rgba(0, 0, 0, 0.5);
  }

  &__message {
    padding: 50px 20px 30px;
    font-size: 16px;
    text-align: center;
    color: $text_primary;
  }

  .btn {
    &__wrap {
      display: flex;
      justify-content: space-evenly;

      button {
        width: 300px;
        margin: 10px 0px 24px;
        height: 30px;
        border-radius: 4px;
        background: $primary;
        color: $white;
        font-size: 13px;
        font-weight: 500;
      }
    }
  }
}
</style>
