<template>
  <div>
    <search-container title=" " :refresh="refresh" :search="search">
      <div class="search_row">
        <div class="col-md-1 label"></div>
        <div>
          <checkbox-basic
            inline
            all
            type="group"
            v-model:value="data.cur_status"
            :options="CB.BRANCH_STATUS"
          />
        </div>
      </div>
      <div class="search_row">
        <div class="col-md-1 label"></div>
        <div class="row" style="display: flex; flex: 1">
          <div class="col-md-2 q-mr-md">
            <select-box
              dense
              v-model:value="data.condition"
              :options="SB.BRANCH_SEARCH_CONDITION"
            />
          </div>
          <div class="col-md-6">
            <input-basic
              v-model="data.input"
              placeholder="  ."
            >
              <q-icon
                style="cursor: pointer"
                name="close"
                @click="data.input = ''"
              />
            </input-basic>
          </div>
        </div>
      </div>
    </search-container>
    <div class="q-mt-md" v-if="listData.length > 0">
      <grid
        :columns="COLUMNS.BRANCH_LIST"
        :rows="listData"
        @rowClick="moveToDetail"
        flat
        style="border: 1px solid #ddd"
      >
        <span class="q-ml-sm">
          <button-basic
            style="height: 40px"
            title=""
            icon="add"
            @click="moveToRegist"
          />
        </span>
      </grid>
    </div>
  </div>
</template>

<script setup>
import { reactive, onMounted, ref } from "vue";
import moment from "moment";
import SearchContainer from "@/components/organism/SearchContainer";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import CheckboxBasic from "@/components/atom/checkbox/CheckboxBasic.vue";
import SelectBox from "@/components/molecule/select/SelectBox.vue";
import InputBasic from "@/components/atom/input/InputBasic.vue";
import Grid from "@/components/molecule/Grid/Grid.vue";
import COLUMNS from "@/constants/columns";
import Branch_API from "@/api/modules/branch";
import { CB, SB } from "@/constants/const";
import { useRouter } from "vue-router";

const router = useRouter();
const { getBranchData } = Branch_API();

const listData = ref([]);

const data = reactive({
  cur_status: [],
  condition: "",
  input: "",
});

const moveToDetail = (val, row) => {
  const {
    branch_code,
    branch_name,
    category,
    contract_manage,
    corp_no,
    email,
    no,
    phone_no,
    representative,
    status,
  } = row;
  if (typeof val === "number")
    router.push({
      name: "BranchDetail",
      params: { id: val },
      state: {
        data: {
          branch_code,
          branch_name,
          category,
          contract_manage,
          corp_no,
          email,
          no,
          phone_no,
          representative,
          status,
        },
      },
    });
};

const moveToRegist = () => {
  router.push("/academy/regist");
};

const refresh = () => {
  const refreshData = {
    cur_status: [],
    condition: "",
    input: "",
  };
  Object.assign(data, refreshData);
};

const search = () => {
  console.log(data.cur_status);
  console.log(data.condition);
  console.log(data.input);
};

const getList = async () => {
  const res = await getBranchData();
  if (res.length > 0) {
    listData.value = res;
  }
};

onMounted(async () => {
  await getList();
});
</script>
<style scoped></style>
