export const historyBack = () => {
  window.history.back();
};

export const kebabize = (str) =>
  str.replace(
    /[A-Z]+(?![a-z])|[A-Z]/g,
    ($, ofs) => (ofs ? "-" : "") + $.toLowerCase(),
  );

export const getTimeStringSeconds = (seconds) => {
  var hour, min, sec;
  hour = parseInt(seconds / 3600);
  min = parseInt((seconds % 3600) / 60);
  sec = seconds % 60;
  if (hour.toString().length == 1) hour = "0" + hour;
  if (min.toString().length == 1) min = "0" + min;
  if (sec.toString().length == 1) sec = "0" + sec;
  return (hour !== "00" ? hour + ":" : "") + min + ":" + sec;
};

export const NUMBER = /[0-9]/; // 
export const ENGLISH = /[a-zA-Z]/; //  
export const KOREAN = /[-]/; //  
export const SPECIAL = /[~_{}$&+,:;=?@#|'<>.^*()%!-]/g; // 
export const LETTER = /[a-zA-Z-]/; // ,  
export const EMAIL =
  /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/; // 
export const PASSWORD =
  /^(?=.*[a-zA-Z])(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,20}$/; // 8~20, ++
export const PHONENUMBER = /^01([0|1|6|7|8|9])-?([0-9]{3,4})-?([0-9]{4})$/; //  
