import service from "@/api";
import { GlobalStore } from "@/store";
import { DialogStore } from "@/store/modules/dialog";

export default function AUTH_API() {
  const globalStore = GlobalStore();
  const dialog = DialogStore();

  async function login(params, callback) {
    try {
      const { data } = await service.post(`/auth/login`, params);

      if (data.statusCode === "200" || data.statusCode === "827") {
        globalStore.setToken(data.data.accessToken);
        globalStore.setReToken(data.data.refreshToken);
        globalStore.setUserInfo(data.data.user);
        return callback(data.data.message, data.statusCode);
      } else {
        return callback(data.data, data.statusCode);
      }
    } catch (error) {
      console.error(error);
    }
  }

  async function changepassword(params, callback) {
    try {
      const { data } = await service.post("/auth/password", params);

      if (data) {
        return callback(data.data, data.statusCode);
      }
    } catch (error) {
      console.error(error);
    }
  }

  async function findid(params, callback) {
    try {
      const { data } = await service.post("/auth/id-inquiry", params);

      if (data.statusCode === "823") {
        dialog.openAlertDialog({
          message: " ,      ",
          buttonText: "",
        });
        return;
      } else if (data.statusCode === "200") {
        return callback(data.data);
      }
    } catch (error) {
      console.error(error);
    }
  }

  async function checkuser(params, callback) {
    try {
      const { data } = await service.post("/auth/check", params);

      if (data.statusCode === "823") {
        dialog.openAlertDialog({
          message: "      ",
          buttonText: "",
        });
        return;
      } else if (data.statusCode === "200") {
        return callback(data.data);
      }
    } catch (error) {
      console.error(error);
    }
  }

  async function changepasswordlater() {
    try {
      const { data } = await service.get("/auth/password-skip", {
        headers: {
          Authorization: `Bearer ${globalStore.token}`,
        },
      });

      if (data.statusCode === "200") {
        return true;
      }
    } catch (error) {
      console.error(error);
    }
  }

  return {
    login,
    changepassword,
    findid,
    checkuser,
    changepasswordlater,
  };
}
