<template>
  <q-dialog v-model="show" @update:model-value="update">
    <q-card style="width: 500px; max-width: 80vw">
      <q-card-section>
        <div class="text-h6"> </div>
      </q-card-section>

      <q-separator />

      <q-card-section>
        <VueDaumPostcode @complete="onComplete" />
      </q-card-section>
    </q-card>
  </q-dialog>
</template>
<script setup>
import { ref, defineEmits, watch } from "vue";
import { VueDaumPostcode } from "vue-daum-postcode";

const props = defineProps({
  show: { type: Boolean, require: true, default: false },
});

const show = ref(props.show);
const emit = defineEmits("complete", "update:show");

const onComplete = (res) => {
  const payload = {
    zonecode: res.zonecode,
    roadAddress: res.roadAddress,
    jibunAddress: res.jibunAddress,
  };
  emit("complete", payload);
};

const update = (val) => {
  emit("update:show", val);
};

watch(props, () => {
  show.value = props.show;
});
</script>
<style></style>
