<template>
  <section class="confirm__wrap">
    <div class="confirm">
      <div class="confirm__message" v-html="confirmOption.message || ''"></div>
      <footer class="btn__wrap">
        <button
          class="btn__cancel"
          type="button"
          @keydown.enter="closeConfirm(false)"
          @click="closeConfirm(false)"
        >
          {{ confirmOption.cancel || "" }}
        </button>
        <button
          class="btn__ok"
          type="button"
          @keydown.enter="closeConfirm(true)"
          @click="closeConfirm(true)"
        >
          {{ confirmOption.ok || "" }}
        </button>
      </footer>
    </div>
  </section>
</template>

<script>
import { computed } from "vue";
// import { useStore } from 'vuex';
import { DialogStore } from "@/store/modules/dialog";

export default {
  name: "confirm-dialog",
  setup() {
    const store = DialogStore();
    const confirmOption = computed(
      // () => store.state.dialogStore.confirmOption,
      () => store.confirmOption,
    );

    const closeConfirm = (value) => {
      // console.log('close confirm');
      store.closeConfirmDialog(value);
      // store.commit('dialogStore/closeConfirmDialog', value);
    };
    return {
      confirmOption,
      closeConfirm,
    };
  },
};
</script>

<style scoped lang="scss">
.confirm {
  overflow: hidden;
  width: 340px;
  border-radius: 8px;
  box-sizing: border-box;
  background: $white;

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2000;
    background-color: rgba(0, 0, 0, 0.5);
  }

  &__message {
    padding: 50px 20px 30px;
    font-size: 16px;
    text-align: center;
    color: $text_primary;
  }

  .btn {
    &__wrap {
      display: flex;
      justify-content: space-evenly;

      button {
        width: 140px;
        margin: 10px 0px 24px;
        height: 30px;
        border-radius: 4px;
        font-size: 13px;
        font-weight: 500;
      }
    }

    &__ok {
      background: $primary;
      color: $white;
    }

    &__cancel {
      background: $gray_100;
      color: $gray_600;
      border: 1px solid $gray_400;
    }
  }
}
</style>
