  academy_name: "",
  corp_no: "",
  id: "",
  mobile_phone_no: "",
  birth: "",
  email: "",
  status: "",
  adacemy: "",
  first_access: "",
  last_access: "",
});

const resetPassword = () => {
  console.log("reset password");
};

const moveToList = () => {
  router.push("/member/academy");
};

const update = () => {
  router.push(`/member/academy`);
};

onMounted(() => {
  id.value = route.params.id;
});
</script>
<style scoped>
td {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.footer {
  display: flex;
  justify-content: center;
  padding-bottom: 1rem;
}
</style>
