<script setup name="InquiryDetail">
import { ref, computed } from "vue";
import { DialogStore } from "@/store/modules/dialog";
import { useRouter } from "vue-router";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";

// TODO bottom button_container    ,  ,    

const data = ref(history.state.data);

const store = DialogStore();
const router = useRouter();

const isEditing = ref(false);
const answerValue = ref("");
const files = ref([]);

const moveToList = (value) => {
  if (value) {
    let data = {
      answer: answerValue.value,
      files: files.value,
    };
    console.log(data);
    // TODO       ,   
  }
};

const edit = () => {
  isEditing.value = true;
  console.log(isEditing.value);
};

const openRegistConfirmDialog = () => {
  store.openConfirmDialog({
    message: "  ?",
    cancel: "",
    ok: "",
    callback: moveToList,
  });
};
const numberOfFiles = computed(() => files.value.length);

let formData = new FormData();

const onChange = (e) => {
  let selectedFiles = e.target.files;
  let max = 1048576;
  if (e.target.files.length > 0 && e.target.files[0].size > max) {
    store.openAlertDialog({
      message: "  1MB   .",
      buttonText: "",
    });
    return;
  } else if (numberOfFiles.value > 4) {
    store.openAlertDialog({
      message: " 5   .",
      buttonText: "",
    });
    return;
  } else {
    let fileName = e.target.value.slice(12, e.target.value.length);
    files.value.push(fileName);
    for (let i = 0; i < selectedFiles.length; i++) {
      formData.append("files", selectedFiles[i]);
    }
  }
};

const deleteItem = (index) => {
  files.value = files.value.filter((_, i) => i !== index);
};
</script>

