<template>
  <div class="notice_container">
    <div class="top_info">
      <h6>{{ data.link }}</h6>
      <span>{{ data.date }} | {{ data.view_count }}</span>
    </div>
    <div class="divider" />
    <div class="content">
      {{ data.content }}
    </div>
    <div class="download_file">
      <span style="color: blue; height: 30px"></span>
      <div style="border: 1px solid #ddd; padding: 20px; margin-top: 10px">
        <div
          v-for="(item, index) in data.files"
          :key="index"
          style="margin-bottom: 5px"
        >
          {{ item }}
        </div>
      </div>
    </div>
    <div class="bottom_container">
      <button class="button" @click="moveToList"></button>
      <button class="button" @click="edit"></button>
    </div>
  </div>
</template>

<script setup name="NoticeDetail">
import { reactive, onMounted } from "vue";
import { useRouter } from "vue-router";

const router = useRouter();
const data = reactive(history.state.data);

const moveToList = () => {
  router.push("/service/notice");
};

const edit = () => {
  const { channel, content, date, link, view_count, id, fixed } = data;
  const files = [...data.files];
  router.push({
    name: "NoticeRegist",
    state: {
      data: { id, channel, link, date, content, view_count, files, fixed },
    },
  });
};

onMounted(() => {
  data.value = history.state.data;
  console.log(data.value);
});
</script>

<style scss="lang" scoped>
.notice_container {
  width: 100%;
  height: 100%;
  padding: 0px 20px;
  display: flex;
  flex-direction: column;
}

.top_info {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 40px;
  justify-content: space-between;
}

.divider {
  width: 100%;
  height: 1px;
  background-color: #ddd;
}

.content {
  width: 100%;
  height: 400px;
  border-bottom: 1px solid #ddd;
  padding-top: 40px;
  font-weight: 500;
  line-height: 2;
}

.download_file {
  width: 100%;
  margin-top: 20px;
}

.bottom_container {
  width: 100%;
  height: 80px;
  margin-top: 20px;
  padding: 20px 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.button {
  border: 1px solid gray;
  padding: 10px;
  width: 100px;
}
</style>
