<template>
  <div class="notice_container">
    <div class="title">
      <h6> </h6>
      <span style="color: red">* </span>
    </div>
    <div class="divider" />
    <div class="row_container">
      <span class="sub_title"><span style="color: red">*</span></span>
      <CheckboxBasic
        inline
        all
        type="group"
        v-model:value="type"
        :options="CB.NOTICE_CHANNEL"
      />
    </div>
    <div class="divider" />
    <div class="row_container">
      <span class="sub_title"><span style="color: red">*</span></span>
      <div style="display: flex; flex-direction: row; align-items: center">
        <q-checkbox v-model="fixed" />
        <span></span>
      </div>
    </div>
    <div class="divider" />
    <div class="row_container">
      <span class="sub_title"><span style="color: red">*</span></span>
      <div style="display: flex; flex-direction: row; align-items: center">
        <RadioBox
          v-model="isshowingItemOnList"
          :options="[
            { label: '', value: '' },
            { label: '', value: '' },
          ]"
          inline
        />
      </div>
    </div>
    <div class="divider" />
    <div class="row_container">
      <span class="sub_title"><span style="color: red">*</span></span>
      <div style="display: flex; flex-direction: column; width: 90%">
        <InputBasic
          style="padding: 5px"
          placeholder=" "
          v-model="titlevalue"
        />
        <div style="text-align: right">{{ titlevalue.length }}/100</div>
      </div>
    </div>
    <div class="divider" />
    <div class="row_container">
      <span class="sub_title"><span style="color: red">*</span></span>
      <div style="display: flex; flex-direction: column; width: 90%">
        <!-- TODO Text Editor  -->
        <textarea
          v-model="contentvalue"
          placeholder=" "
          class="content_input"
        />
        <div style="text-align: right">{{ contentvalue.length }}/1000</div>
      </div>
    </div>
    <div class="divider" />
    <div class="row_container">
      <span class="sub_title"></span>
      <div style="display: flex; flex-direction: column">
        <div
          style="
            display: flex;
            flex-direction: row;
            width: 100%;
            align-items: center;
          "
        >
          <div class="filebox">
            <label for="file"></label>
            <input type="file" id="file" @change="onChange" />
          </div>
          <div style="margin-left: 10px; width: 300px">
             5  .
          </div>
        </div>
        <div
          v-if="files.length > 0"
          style="display: flex; flex-direction: column; margin-top: 10px"
        >
          <div
            v-for="(item, index) in files"
            :key="index"
            style="display: flex; flex-directon: row"
          >
            {{ item }}
            <div class="closeBtn" @click="deleteItem(index)">x</div>
          </div>
        </div>
      </div>
    </div>
    <div class="divider" />
  </div>
  <div class="bottom_container">
    <ButtonBasic
      title=""
      @click="() => router.push('/service/notice')"
      color="standard"
      textColor="black"
    />
    <ButtonBasic
      title=""
      @click="openRegistConfirmDialog"
      style="padding: 0 20px"
    />
  </div>
</template>

<script setup name="NoticeRegist">
import { useRouter } from "vue-router";
import { ref, watch } from "vue";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import { DialogStore } from "@/store/modules/dialog";
import Service_API from "@/api/modules/service";
import { CB } from "@/constants/const";
import CheckboxBasic from "@/components/atom/checkbox/CheckboxBasic.vue";
import RadioBox from "@/components/molecule/Radio/RadioBox.vue";
import InputBasic from "@/components/atom/input/InputBasic.vue";

const store = DialogStore();
const router = useRouter();
const { createNotice } = Service_API();

const fixed = ref(history.state.data ? history.state.data.fixed : false);
const titlevalue = ref(history.state.data ? history.state.data.link : "");
const type = ref([]);
const contentvalue = ref(history.state.data ? history.state.data.content : "");
const isshowingItemOnList = ref();
const files = ref([]);

const onChange = (e) => {
  let fileName = e.target.value.slice(12, e.target.value.length);
  files.value.push(fileName);
};

const deleteItem = (index) => {
  files.value = files.value.filter((_, i) => i !== index);
};

const submit = async (value) => {
  if (value) {
    let i = 10;
    let data = {
      name: 11,
      channel: type.value,
      fixed: fixed.value,
      date: "23-01-05",
      title: titlevalue.value,
      content: contentvalue.value,
      files: files.value,
      viewCount: 22,
    };
    const res = await createNotice(data);

    router.push("/service/notice");
  }
};

const openRegistConfirmDialog = () => {
  store.openConfirmDialog({
    message: "  ?",
    cancel: "",
    ok: "",
    callback: submit,
  });
};

watch(titlevalue, (e) => {
  if (titlevalue.value.length > 100) {
    alert("100  ");
    titlevalue.value = titlevalue.value.slice(0, 99);
    return;
  }
});

watch(contentvalue, (e) => {
  if (contentvalue.value.length > 1000) {
    alert("1000  ");
    contentvalue.value = contentvalue.value.slice(0, 999);
    return;
  }
});
</script>

<style lang="scss" scoped>
.divider {
  width: 100%;
  height: 1px;
  background-color: #99999930;
}

.notice_container {
  padding: 0px 20px 20px;
}

.title {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.row_container {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.sub_title {
  width: 120px;
}

.content_input {
  height: 300px;
  padding: 5px;
}

.bottom_container {
  width: 100%;
  height: 80px;
  padding: 20px 80px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.button {
  border: 1px solid gray;
  padding: 10px;
  width: 100px;
}
</style>
