        <col width="25%" />
        <col width="25%" />
      </colgroup>
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="register-table-td">
            <button-basic
              unelevated
              class="q-px-xl"
