          </td>
          <td class="register-table-td">
            <button-basic
              unelevated
              class="q-px-xl"
              color="secondary"
              title=""
              @click="router.push('/academy/regist')"
            />
          </td>
          <td rowspan="2" class="register-table-td">
            <button-basic
              unelevated
              class="q-px-xl"
              color="secondary"
              title=""
              @click="router.push('/academy/regist')"
            />
          </td>
          <td rowspan="2" class="register-table-td">
            <button-basic
              unelevated
              class="q-px-xl"
              color="secondary"
              title=""
              @click="router.push('/auth/super/regist')"
            />
          </td>
        </tr>
        <tr>
          <td class="register-table-td">
            <div class="q-px-md q-py-md box" @click="toggle('student')">
              <br />
            </div>
            <router-link class="link" to="/"> </router-link>
          </td>
          <td class="register-table-td">
            <div class="q-px-md q-py-md box" @click="toggle('teacher')">
              <br />
            </div>
            <router-link class="link" to="/"> </router-link>
          </td>
        </tr>
      </tbody>
    </q-markup-table>

    <q-dialog v-model="show">
      <q-card style="width: 800px; max-width: 80vw">
        <q-card-section>
          <div class="text-h6"> </div>
        </q-card-section>

        <q-separator />

