          <div>{{ popup_name }}  </div>
          <q-markup-table class="q-my-sm" flat bordered>
            <colgroup>
              <col width="20%" style="background-color: #99999910" />
              <col width="80%" />
            </colgroup>
            <tbody>
              <tr>
                <td style="font-weight: bold">CSV  </td>
                <td>
                  <div style="display: flex; flex: 1">
                    <q-file
                      dense
                      outlined
                      accept=".csv"
                      v-model="file"
                      ref="fileRef"
                      style="display: flex; flex: 1; min-height: 20px"
                    />
                    <button-basic
                      unelevated
                      title=""
                      color="grey"
                      style="height: 40px"
                      @click="() => fileRef.pickFiles()"
                    />
                    <button-basic
                      unelevated
                      class="q-ml-sm q-px-lg"
                      color="black"
                      text-color="white"
                      title=""
                      style="height: 40px"
                      @click="upload"
                    />
                  </div>
                  <p class="q-mt-sm" v-if="is_upload">
                    <span style="font-weight: bold"> </span>
                    <span class="q-ml-sm"
                      >  :
                      <span style="color: green">{{ total_count }} </span
                      ></span
                    >
                    <span>
                      |   :
                      <span style="color: blue">{{ success_count }}</span
                      ></span
                    >
                    <span>
                      |   :
                      <span style="color: red">{{ success_count }}</span
                      ></span
                    >
                  </p>
                </td>
              </tr>
              <tr>
                <td>   </td>
                <td>
                  <p>1.  </p>
                  <p>
                    2.        (
                    1,000)
                  </p>
                  <p>1.  </p>
                  <p>1.  </p>
                  <p>1.  </p>
                </td>
              </tr>
            </tbody>
          </q-markup-table>
          <template v-if="fail_count > -1">
            <div class="q-mb-sm text-bold"> </div>
            <div>
              <q-table
                class="my-sticky-header-table"
                flat
                bordered
                :rows="fail_result"
                :columns="COLUMNS.REGISTER_ALL_UPLOAD_FAIL"
                row-key="name"
                hide-bottom
              />
            </div>
          </template>
        </q-card-section>
        <q-card-actions align="center" class="bg-white text-teal">
          <button-basic
            unelevated
            class="q-px-xl"
            color="primary"
            title=""
            @click="save"
          />
        </q-card-actions>
      </q-card>
    </q-dialog>
  </div>
</template>
<script setup>
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import { ref } from "vue";
import { useRouter } from "vue-router";
import COLUMNS from "@/constants/columns";

const router = useRouter();
const show = ref(false);
const popup_name = ref("");
const total_count = ref(0);
const fail_count = ref(0);
const success_count = ref(0);
const is_upload = ref(false);
const fail_result = ref([]);
const fileRef = ref(null);
const file = ref(null);

const toggle = (flag) => {
  if (flag === "student") {
    popup_name.value = "";
  } else {
    popup_name.value = "";
  }
  show.value = !show.value;
};

const upload = () => {
  console.log(file.value);
};

const save = () => {
  file.value = null;
  show.value = false;
};
</script>
<style scoped>
th {
  background-color: #e0e0e0;
}

.register-table-td {
  text-align: center;
  padding-top: 3rem !important;
  padding-bottom: 3rem !important;
}

.link {
  color: blue;
}

.link:hover {
  text-decoration: underline;
}

.box {
  width: 50%;
  margin: auto;
  border: 1px solid gray;
  color: gray;
  margin-bottom: 1rem;
  cursor: pointer;
  white-space: pre-line;
}

p {
  margin: 0;
}
</style>
