<template>
  <div class="branch_container">
    <h6> </h6>
    <div style="width: 100%; text-align: right; color: red">*</div>
    <table class="table">
      <tbody class="table_body">
        <tr class="table_row">
          <td class="table_title"></td>
          <td class="table_data">{{ data.branch_code }}</td>
        </tr>
        <tr class="table_row">
          <td class="table_title"><span style="color: red">*</span></td>
          <td class="table_data">
            <input-basic placeholder=" " v-model="data.branch_name" />
          </td>
        </tr>
        <tr class="table_row">
          <td class="table_title"></td>
          <td
            class="table_data"
            style="
              height: 50px;
              display: flex;
              flex-direction: row;
              align-items: center;
              width: 310px;
            "
          >
            <select-box
              style="min-width: 120px"
              v-model:value="data.branch_type"
              :options="SB.BRANCH_TYPE"
            />
            <div>
              <input-basic
                :disabled="data.branch_type === ''"
                style="margin-left: 5px; flex: 1"
                placeholder=" "
                v-model="data.branch_etc"
              />
            </div>
          </td>
        </tr>
        <tr class="table_row">
          <td class="table_title"><span style="color: red">*</span></td>
          <td class="table_data">
            <input-basic
              style="flex: 1"
              placeholder=" "
              v-model="data.representative"
            />
          </td>
        </tr>
        <tr class="table_row">
          <td class="table_title"></td>
          <td class="table_data">
            <input-basic
              style="flex: 1"
              placeholder="  (-)"
              v-model="data.corp_no"
            />
          </td>
        </tr>
        <tr class="table_row">
          <td class="table_title"></td>
          <td
            class="table_data"
            style="display:flex;flex-direction: : row;align-items: center; margin-top: 5px;"
          >
            <input-basic
              disable
              class="col-md-2 q-mr-sm"
              v-model="data.zonecode"
            />
            <input-basic
              disable
              class="col-md-4 q-mr-sm"
              v-model="data.address"
            />
            <input-basic class="col-md-4 q-mr-md" />
            <button-basic
              style="width: 100px"
              rounded
              outline
              color="standard"
              text-color="primary"
              title=" "
              @click="showAddressModal = true"
            />
          </td>
        </tr>
        <tr class="table_row">
          <td class="table_title"><span style="color: red">*</span></td>
          <td
            class="table_data"
            style="
              display: flex;
              flex-direction: row;
              align-items: center;
              margin-top: 5px;
            "
          >
            <input-basic
              style="width: 300px"
              type="number"
              v-model="data.cell_phone"
              placeholder="  10-11 (-)"
            />
            <button-basic
              style="width: 100px; margin-left: 15px"
              rounded
              outline
              color="standard"
              text-color="primary"
              title=""
              @click="console.log('click')"
            />
          </td>
        </tr>
        <tr class="table_row">
          <td class="table_title"></td>
          <td class="table_data">
            <input-basic
              type="number"
              v-model="data.phone_no"
              placeholder="  (-)"
            />
          </td>
        </tr>
        <tr class="table_row">
          <td class="table_title">e-mail</td>
          <td class="table_data">
            <input-basic
              v-model="data.email"
              placeholder="(@  )"
            />
          </td>
        </tr>
      </tbody>
    </table>
    <div class="bottom_container">
      <ButtonBasic
        title=""
        @click="router.push('/academy')"
        color="standard"
        textColor="black"
      />
      <ButtonBasic
        title=""
        @click="openConfirmDialogToSubmit"
        style="padding: 0 20px"
      />
    </div>
  </div>
  <address-modal v-model:show="showAddressModal" @complete="complete" />
</template>
<script setup>
import SelectBox from "@/components/molecule/select/SelectBox.vue";
import InputBasic from "@/components/atom/input/InputBasic.vue";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import { DialogStore } from "@/store/modules/dialog";
import AddressModal from "@/components/molecule/AddressModal/AddressModal.vue";
import { ref, watch, onMounted, reactive } from "vue";
import { useRouter } from "vue-router";
import { CB, SB } from "@/constants/const";

const router = useRouter();
const store = DialogStore();
const showAddressModal = ref(false);
const data = reactive({
  branch_code: "",
  branch_name: "",
  branch_type: "",
  branch_etc: "",
  representative: "",
  corp_no: "",
  address: "",
  cell_phone: null,
  phone_no: null,
  email: "",
  id: "",
  products: "",
  payment_day: "",
  payment_period: "",
  contract_day: "",
  status: "",
  zonecode: "",
});

const complete = (address) => {
  data.zonecode = address.zonecode;
  data.address = address.roadAddress;
  showAddressModal.value = false;
};

watch(data, (val) => {
  console.log(val);
});

const submit = (value) => {
  if (value) {
    store.openAlertDialog({
      message: ".",
      buttonText: "",
    });
    setTimeout(() => router.push("/academy"), 500);
  }
};

const openConfirmDialogToSubmit = () => {
  store.openConfirmDialog({
    message: "   ?",
    cancel: "",
    ok: "",
    callback: submit,
  });
};
</script>

<style lang="scss" scoped>
.branch_container {
  width: 100%;
  height: 100%;
}

.table {
  width: 100%;
  margin-top: 20px;

  &_body {
    width: 100%;
  }

  &_row {
    height: 50px;
    border-top: 1px solid #99999940;
    border-bottom: 1px solid #99999940;
  }

  &_title {
    min-width: 140px;
    width: 16%;
    background-color: #99999910;
    padding-left: 20px;
  }

  &_data {
    padding-left: 10px;
    padding-right: 10px;
  }
}
</style>
