<template>
  <div>
    <q-drawer class="bg-grey-2" side="left" show-if-above>
      <div class="logo">  </div>
      <div class="info">
        <div style="display: flex; align-items: center">
          <div style="flex: 1; font-size: 1.5rem">
            <span style="font-weight: bold"></span>
          </div>
          <q-btn size="sm" color="black" label="" />
        </div>
        <div style="margin-bottom: 1rem">test@email.com</div>
        <router-link class="user_link" to="/home"></router-link>
      </div>
      <q-list padding>
        <template v-for="item in menuList">
          <template v-if="item.children?.length > 0">
            <div
              v-bind:key="item.name"
              style="flex-direciton: row; justify-content: space-between"
              :class="item.active && item.open ? 'menu-item' : 'menu-item'"
              @click="item.open = !item.open"
            >
              <div>
                <q-icon
                  :name="item.icon"
                  size="35px"
                  style="margin-right: 10px"
                ></q-icon
                >{{ item.name }}
              </div>
              <q-icon name="expand_less" size="24px" v-if="item.open" />
              <q-icon name="expand_more" size="24px" v-else />
            </div>
            <template v-if="item.open">
              <template v-for="child in item.children" v-bind:key="child.name">
                <router-link :to="child.path">
                  <div
                    :class="
                      child.active
                        ? 'menu-item-chilren active'
                        : 'menu-item-chilren'
                    "
                  >
                    {{ child.name }}
                  </div>
                </router-link>
              </template>
              <!-- <div
                v-bind:key="item.name"
                style="
                  padding-top: 6px;
                  margin-top: 20px;
                  margin-bottom: 20px;
                  background-color: #ededed;
                "
                v-if="item.open && item.active"
              ></div> -->
            </template>
          </template>
          <template v-else>
            <router-link v-bind:key="item.name" :to="item.path">
              <div
                style="flex-direciton: row; justify-content: space-between"
                :class="item.active ? 'menu-item active' : 'menu-item'"
              >
                <div>
                  <q-icon
                    :name="item.icon"
                    size="35px"
                    style="margin-right: 10px"
                  ></q-icon
                  >{{ item.name }}
                </div>
                <q-icon name="expand_less" size="24px" v-if="item.open" />
                <q-icon name="expand_more" size="24px" v-else />
              </div>
            </router-link>
          </template>
        </template>
      </q-list>
    </q-drawer>
  </div>
</template>

<script setup>
import { ref, watch, onMounted } from "vue";
import { useRoute } from "vue-router";
import { menu } from "@/constants/menu";

const menuList = ref(menu);
const route = useRoute();
console.log(route.path);

const activeMenu = () => {
  //
  if (menuList.value) {
    menuList.value.map((val) => {
      if (val.active) {
        val.active = false;
      }
      if (val.children) {
        val.children.map((v) => {
          if (v.active) {
            v.active = false;
          }
        });
      }
    });
    //active
    menuList.value.map((menu) => {
      if (menu.children.length > 0) {
        menu.children.map((child) => {
          if (route.path.startsWith(child.path)) {
            menu.open = true;
            menu.active = true;
            child.active = true;
          }
        });
      } else {
        if (menu.path === route.path) {
          menu.active = true;
        }
      }
    });
  }
};
watch(route, () => {
  activeMenu();
});

onMounted(() => {
  activeMenu();
});
</script>

<style scoped lang="scss">
.menu-box {
  background: $gray_200;
}

.logo {
  width: 100%;
  height: 60px;
  padding: 1rem;
}

.user_link {
  font-size: 0.75rem;
  color: grey;
  text-decoration: underline;
}

.info {
  padding: 1rem;
}

.menu-item {
  cursor: pointer;
  font-size: 1.125rem;
  display: flex;
  align-items: center;
  margin: 0.625rem;
  padding: 0.625rem;

  &.active {
    color: #ff4e7c;
    font-weight: bold;
  }
}

.menu-item-chilren {
  cursor: pointer;
  font-size: 1.125rem;
  display: flex;
  padding: 0.313rem;
  margin-left: 3.125rem;
  color: #616161;
  &.active {
    color: #ff4e7c;
    font-weight: bold;
  }
}

.menu-item:hover {
  background-color: gray;
  border-radius: 0.625rem;
  color: white;
}

.home-content-box {
  padding: 10px;
}

.lesson-box {
  background-color: #f7f7f7;
  border-radius: 20px;
  padding: 20px;
  margin: 10px;
}
</style>
