<template>
  <div>
    <div class="notice_container">
      <search-container title=" " :refresh="refresh" :search="search">
        <div
          class="search_row"
          style="display: flex; flex-direction: row; align-items: center"
        >
          <div class="col-md-1 label"></div>
          <div>
            <date-picker
              :start-date="data.start_date"
              :end-date="data.end_date"
              @change-date="changeDate"
            />
          </div>
          <div>
            <radio-box inline :options="labels" v-model="selected" />
          </div>
        </div>
        <div class="search_row">
          <div class="col-md-1 label"> </div>
          <div class="row" style="display: flex; flex: 1">
            <div class="col-md-2 q-mr-lg">
              <select-box
                dense
                v-model="data.category"
                :options="category_options"
              />
            </div>
          </div>
        </div>
        <div class="search_row">
          <div class="col-md-1 label"></div>
          <div class="row" style="display: flex; flex: 1">
            <div class="col-md-2 q-mr-lg">
              <select-box
                dense
                v-model="data.condition"
                :options="CB.NOTICE_CHANNEL"
              />
            </div>
            <div class="col-md-6">
              <input-basic
                v-model="data.input"
                placeholder="  ."
              >
                <q-icon
                  style="cursor: pointer"
                  name="close"
                  @click="data.input = ''"
                />
              </input-basic>
            </div>
          </div>
        </div>
      </search-container>
    </div>
    <div class="q-mt-md">
      <grid
        style="border: 1px solid #ddd"
        flat
        :rows="data.category === '' ? rows : currentRows"
        :columns="COLUMNS.FAQ_LIST"
        :onRowClick="onRowClick"
      >
        <span class="q-ml-sm">
          <button-basic
            style="height: 40px"
            title="FAQ"
            icon="add"
            @click="registFAQ"
          />
        </span>
      </grid>
    </div>
  </div>
</template>

<script setup name="FAQList">
import { ref, onMounted, reactive, watch } from "vue";
import { useRouter } from "vue-router";
import COLUMNS from "@/constants/columns";
import moment from "moment";

import DatePicker from "@/components/molecule/datePicker/DatePicker.vue";
import RadioBox from "@/components/molecule/Radio/RadioBox.vue";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import SearchContainer from "@/components/organism/SearchContainer";
import SelectBox from "@/components/molecule/select/SelectBox.vue";
import InputBasic from "@/components/atom/input/InputBasic.vue";
import Grid from "@/components/molecule/Grid/Grid.vue";
import { CB } from "@/constants/const";

const today = moment().format("YYYY-MM-DD");
const yesterday = moment().subtract(1, "days").format("YYYY-MM-DD");
const sevenDaysAgo = moment().subtract(7, "days").format("YYYY-MM-DD");
const oneMonthAgo = moment().subtract(1, "months").format("YYYY-MM-DD");
const sixMonthsAgo = moment().subtract(6, "months").format("YYYY-MM-DD");

const router = useRouter();
const currentRows = ref([]);

const data = reactive({
  condition: "",
  category: "",
  input: "",
  start_date: yesterday,
  end_date: today,
});

const labels = [
  { label: "", value: "" },
  { label: "7", value: "7" },
  { label: "1", value: "1" },
  { label: "6", value: "6" },
];
const selected = ref("");

const category_options = [
  {
    label: "",
    value: 0,
  },
  {
    label: "1",
    value: 1,
  },
  {
    label: "2",
    value: 2,
  },
  {
    label: "3",
    value: 3,
  },
  {
    label: "4",
    value: 4,
  },
  {
    label: "5",
    value: 5,
  },
];

const rows = [
  {
    id: 10,
    channel: "APP",
    category: "1",
    title: "  ",
    date: "22.12.12",
    view_count: 24,
  },
  {
    id: 9,
    channel: "APP",
    category: "4",
    title: "  ",
    date: "22.12.12",
    view_count: 37,
  },
  {
    id: 8,
    channel: "LMS",
    category: "2",
    title: "  ",
    date: "22.12.12",
    view_count: 23,
  },
  {
    id: 7,
    channel: "LMS",
    category: "4",
    title: "  ",
    date: "22.12.12",
    view_count: 67,
  },
  {
    id: 6,
    channel: "APP",
    category: "3",
    title: "  ",
    date: "22.12.12",
    view_count: 49,
  },
  {
    id: 5,
    channel: "LMS",
    category: "1",
    title: "  ",
    date: "22.12.12",
    view_count: 94,
  },
  {
    id: 4,
    channel: "LMS",
    category: "3",
    title: "  ",
    date: "22.12.12",
    view_count: 98,
  },
  {
    id: 3,
    channel: "LMS",
    category: "2",
    title: "  ",
    date: "22.12.12",
    view_count: 87,
  },
  {
    id: 2,
    channel: "APP",
    category: "1",
    title: "  ",
    date: "22.12.12",
    view_count: 51,
  },
  {
    id: 1,
    channel: "LMS",
    category: "2",
    title: "  ",
    date: "22.12.12",
    view_count: 65,
  },
];

watch(data, (val) => {
  console.log(val);
});

watch(selected, (value) => {
  if (value === "") {
    data.start_date = today;
    data.end_date = today;
  } else if (value === "7") {
    data.start_date = sevenDaysAgo;
    data.end_date = today;
  } else if (value === "1") {
    data.start_date = oneMonthAgo;
    data.end_date = today;
  } else if (value === "6") {
    data.start_date = sixMonthsAgo;
    data.end_date = today;
  }
});

const changeDate = (val) => {
  val.type === "start"
    ? (data.start_date = val.date)
    : (data.end_date = val.date);
};

const refresh = () => {
  console.log("refresh");
  data.category = "";
  data.input = "";
  data.condition = "";
  data.start_date = yesterday;
  data.end_date = today;
};
const search = () => {
  console.log(currentRows.value, rows, data.category);
  currentRows.value = rows.filter(
    (item) => item.category === data.category.label,
  );

  console.log(currentRows.value);
  console.log(data.condition);
  console.log(data.input);
  console.log(data.category);
};

const registFAQ = () => {
  router.push("/service/faq/regist");
};

const onRowClick = (e, row) => {
  router.push({
    name: "FAQDetail",
    params: { id: row.id },
    state: { data: row },
  });
};

onMounted(() => {
  currentRows.value = rows;
});
</script>

<style lang="scss">
.notice_container {
  min-width: 800px;
}

.tab {
  padding: 2px 10px;
  border-radius: 4px;
  margin-right: 10px;
  color: white;
}

.top_info {
  display: flex;
  flex-direction: column;
  height: 50px;
  justify-content: space-between;
}

.input_container {
  margin-left: 10px;
  display: flex;
  flex-direction: row;
}

.register_notice_btn {
  border: 1px solid #99999960;
  background-color: #99999960;
  padding: 10px 20px;
  width: 50%;
  margin-left: 10px;
}

.notice_table {
  min-height: 500px;
  width: 100%;
}
</style>
