<template>
  <div class="notice_container">
    <search-container title=" " :refresh="refresh" :search="search">
      <div class="search_row">
        <div class="col-md-1 label"></div>
        <div class="row" style="display: flex; flex: 1">
          <checkbox-basic
            inline
            all
            type="group"
            v-model:value="data.selected"
            :options="CB.NOTICE_CHANNEL"
          />
        </div>
      </div>
      <div class="search_row">
        <div class="col-md-1 label"></div>
        <div class="row" style="display: flex; flex: 1">
          <div class="col-md-6">
            <input-basic
              v-model="data.input"
              placeholder="  ."
            >
              <q-icon
                style="cursor: pointer"
                name="close"
                @click="data.input = ''"
              />
            </input-basic>
          </div>
        </div>
      </div>
    </search-container>
    <div v-if="rows.data.length > 0" style="margin-top: 20px">
      <grid
        style="border: 1px solid #ddd"
        flat
        :columns="COLUMNS.NOTICE_LIST"
        :rows="rows.data"
        row-key="name"
      >
        <span class="q-ml-sm">
          <button-basic
            style="height: 40px; padding-left: 41px; padding-right: 41px"
            title=""
            @click="registerNotice"
          />
        </span>
      </grid>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, watch, onMounted } from "vue";
import { useRouter } from "vue-router";
import Service_API from "@/api/modules/service";
import COLUMNS from "@/constants/columns";

import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import SearchContainer from "@/components/organism/SearchContainer";
import InputBasic from "@/components/atom/input/InputBasic.vue";
import Grid from "@/components/molecule/Grid/Grid.vue";
import CheckboxBasic from "@/components/atom/checkbox/CheckboxBasic.vue";
import { CB } from "@/constants/const";

const { getNoticeList } = Service_API();
const router = useRouter();

let rows = reactive({ data: [] });
let originalData = ref([]);

const data = reactive({
  condition: "",
  input: "",
  selected: [],
});

watch(data, (val) => {
  console.log(val);
});

//     
const refresh = () => {
  data.condition = "";
  data.input = "";
  rows.data = originalData.value;
};

//    
const search = () => {
  // api 
  console.log("search");
};

//     
const registerNotice = () => {
  router.push({ path: "/service/notice/regist", state: { data: "" } });
};

async function getList() {
  const res = await getNoticeList();
  rows.data = res;
  originalData.value = res;
}

onMounted(() => {
  // mount   api 
  getList();
});
</script>

<style lang="scss" scoped>
.top_info {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  height: 50px;
  justify-content: space-between;
}

.notice_container {
  min-width: 800px;
}

.input_container {
  min-width: 200px;
  width: 50%;
  height: auto;
}

.register_notice_btn {
  border: 1px solid #99999960;
  background-color: #99999960;
  padding: 10px 20px;
  width: 50%;
  margin-left: 10px;
}

.notice_table {
  min-height: 500px;
  width: 100%;
  margin-top: 20px;
}
</style>
