<template>
  <div class="home_container">
    <div class="left-side">
      <div class="left-side-content">
        <div class="text-h6">  </div>
        <div ref="chartdiv" style="width: 100%; height: 100%"></div>
      </div>
      <div class="left-side-content">
        <div class="text-h6"> </div>
        <div>
          <q-table
            :rows="rows"
            :columns="COLUMNS.DASHBOARD"
            flat
            row-key="name"
            hide-bottom
          />
        </div>
      </div>
      <div class="left-side-content">
        <div class="text-h6"> ()</div>
        <div ref="chartBar" style="width: 100%; height: 100%"></div>
      </div>
    </div>
    <div class="right_side">
      <div class="q-mb-lg">
        <div class="text-h6 q-mb-sm"> </div>
        <div class="box-row q-mb-md">
          <div class="box">
            <span> </span>
            <div class="text-h4 q-mt-xl">10</div>
          </div>
          <div style="flex: 0.1"></div>
          <div class="box">
            <span> </span>
            <div class="text-h4 q-mt-xl">10</div>
          </div>
        </div>
        <div class="box-row">
          <div class="box">
            <span> </span>
            <div class="text-h4 q-mt-xl">10</div>
          </div>
          <div style="flex: 0.1"></div>
          <div class="box">
            <span>  </span>
            <div class="text-h4 q-mt-xl">10</div>
          </div>
        </div>
      </div>
      <div>
        <div class="text-h6 q-mb-sm"> </div>
        <div class="box-row">
          <div class="box-color" style="background-color: #e3f5ff">
            <span> </span>
            <div class="text-h4 q-mt-xl">10</div>
          </div>
          <div style="flex: 0.1"></div>
          <div class="box-color" style="background-color: #e5ecf6">
            <span> </span>
            <div class="text-h4 q-mt-xl">10</div>
          </div>
          <div style="flex: 0.1"></div>
          <div class="box-color" style="background-color: #e3f5ff">
            <span> </span>
            <div class="text-h4 q-mt-xl">10</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script setup>
/* Imports */
import { onMounted, onUnmounted, ref } from "vue";
import * as am5 from "@amcharts/amcharts5";
import * as am5xy from "@amcharts/amcharts5/xy";
import am5themes_Animated from "@amcharts/amcharts5/themes/Animated";
import COLUMNS from "@/constants/columns";
import { GlobalStore } from "@/store";

const globalstore = GlobalStore();

const chartdiv = ref(null);
const chartBar = ref(null);
const rows = ref([]);
let root;
let root_bar;
/* Chart code */
// Create root element
// https://www.amcharts.com/docs/v5/getting-started/#Root_element

onMounted(() => {
  // Set themes
  // https://www.amcharts.com/docs/v5/concepts/themes/
  root = am5.Root.new(chartdiv.value);
  root._logo.dispose();
  // Set themes
  // https://www.amcharts.com/docs/v5/concepts/themes/
  root.setThemes([am5themes_Animated.new(root)]);

  // Create chart
  // https://www.amcharts.com/docs/v5/charts/xy-chart/
  let chart = root.container.children.push(
    am5xy.XYChart.new(root, {
      panX: true,
      panY: true,
      wheelX: "panX",
      wheelY: "zoomX",
      pinchZoomX: true,
    }),
  );

  chart.get("colors").set("step", 3);

  // Add cursor
  // https://www.amcharts.com/docs/v5/charts/xy-chart/cursor/
  let cursor = chart.set("cursor", am5xy.XYCursor.new(root, {}));
  cursor.lineY.set("visible", false);

  // Create axes
  // https://www.amcharts.com/docs/v5/charts/xy-chart/axes/
  let xAxis = chart.xAxes.push(
    am5xy.DateAxis.new(root, {
      maxDeviation: 0.3,
      baseInterval: {
        timeUnit: "day",
        count: 1,
      },
      renderer: am5xy.AxisRendererX.new(root, {}),
      tooltip: am5.Tooltip.new(root, {}),
    }),
  );

  let yAxis = chart.yAxes.push(
    am5xy.ValueAxis.new(root, {
      maxDeviation: 0.3,
      renderer: am5xy.AxisRendererY.new(root, {}),
    }),
  );

  // Create series
  // https://www.amcharts.com/docs/v5/charts/xy-chart/series/
  let series = chart.series.push(
    am5xy.LineSeries.new(root, {
      name: "Series 1",
      xAxis: xAxis,
      yAxis: yAxis,
      valueYField: "value",
      valueXField: "date",
      tooltip: am5.Tooltip.new(root, {
        labelText: "{valueY}",
      }),
    }),
  );
  series.strokes.template.setAll({
    strokeWidth: 2,
    strokeDasharray: [3, 3],
  });

  // Create animating bullet by adding two circles in a bullet container and
  // animating radius and opacity of one of them.
  series.bullets.push(function (root, series, dataItem) {
    if (dataItem.dataContext.bullet) {
      let container = am5.Container.new(root, {});
      let circle0 = container.children.push(
        am5.Circle.new(root, {
          radius: 5,
          fill: am5.color(0xff0000),
        }),
      );
      let circle1 = container.children.push(
        am5.Circle.new(root, {
          radius: 5,
          fill: am5.color(0xff0000),
        }),
      );

      circle1.animate({
        key: "radius",
        to: 20,
        duration: 1000,
        easing: am5.ease.out(am5.ease.cubic),
        loops: Infinity,
      });
      circle1.animate({
        key: "opacity",
        to: 0,
        from: 1,
        duration: 1000,
        easing: am5.ease.out(am5.ease.cubic),
        loops: Infinity,
      });

      return am5.Bullet.new(root, {
        sprite: container,
      });
    }
  });

  // Set data
  let data = [
    {
      date: new Date(2021, 5, 12).getTime(),
      value: 50,
    },
    {
      date: new Date(2021, 5, 13).getTime(),
      value: 53,
    },
    {
      date: new Date(2021, 5, 14).getTime(),
      value: 56,
    },
    {
      date: new Date(2021, 5, 15).getTime(),
      value: 52,
    },
    {
      date: new Date(2021, 5, 16).getTime(),
      value: 48,
    },
    {
      date: new Date(2021, 5, 17).getTime(),
      value: 47,
    },
    {
      date: new Date(2021, 5, 18).getTime(),
      value: 59,
      bullet: true,
    },
  ];

  series.data.setAll(data);

  // Make stuff animate on load
  // https://www.amcharts.com/docs/v5/concepts/animations/
  series.appear(1000);
  chart.appear(1000, 100);

  ///////////////////////2 ////////////////////////////////////////////////////////////////////////////////////////////

  root_bar = am5.Root.new(chartBar.value);
  root_bar._logo.dispose();
  // Set themes
  // https://www.amcharts.com/docs/v5/concepts/themes/
  root_bar.setThemes([am5themes_Animated.new(root_bar)]);
  root_bar.setThemes([am5themes_Animated.new(root_bar)]);

  // Create chart
  // https://www.amcharts.com/docs/v5/charts/xy-chart/
  let chart_bar = root_bar.container.children.push(
    am5xy.XYChart.new(root_bar, {
      panX: false,
      panY: false,
      wheelX: "none",
      wheelY: "none",
    }),
  );

  // Create axes
  // https://www.amcharts.com/docs/v5/charts/xy-chart/axes/
  let yRenderer = am5xy.AxisRendererY.new(root_bar, { minGridDistance: 30 });

  let yAxis_bar = chart_bar.yAxes.push(
    am5xy.CategoryAxis.new(root_bar, {
      maxDeviation: 0,
      categoryField: "country",
      renderer: yRenderer,
    }),
  );

  let xAxis_bar = chart_bar.xAxes.push(
    am5xy.ValueAxis.new(root_bar, {
      maxDeviation: 0,
      min: 0,
      renderer: am5xy.AxisRendererX.new(root_bar, {}),
    }),
  );

  // Create series
  // https://www.amcharts.com/docs/v5/charts/xy-chart/series/
  let series_bar = chart_bar.series.push(
    am5xy.ColumnSeries.new(root_bar, {
      name: "Series 1",
      xAxis: xAxis_bar,
      yAxis: yAxis_bar,
      valueXField: "value",
      sequencedInterpolation: true,
      categoryYField: "country",
      tooltip: am5.Tooltip.new(root_bar, {
        pointerOrientation: "left",
        labelText: "{valueX}",
      }),
    }),
  );

  let columnTemplate = series_bar.columns.template;

  columnTemplate.setAll({
    // draggable: true,
    cursorOverStyle: "pointer",
    // tooltipText: "drag to rearrange",
    cornerRadiusBR: 10,
    cornerRadiusTR: 10,
    tooltipText: am5.Tooltip.new(root_bar, {
      pointerOrientation: "left",
      labelText: "{valueX}",
    }),
  });
  columnTemplate.adapters.add("fill", (fill, target) => {
    return chart.get("colors").getIndex(series_bar.columns.indexOf(target));
  });

  columnTemplate.adapters.add("stroke", (stroke, target) => {
    return chart.get("colors").getIndex(series_bar.columns.indexOf(target));
  });

  columnTemplate.events.on("dragstop", () => {
    sortCategoryAxis();
  });

  // Get series item by category
  function getSeriesItem(category) {
    for (var i = 0; i < series_bar.dataItems.length; i++) {
      let dataItem = series_bar.dataItems[i];
      if (dataItem.get("categoryY") == category) {
        return dataItem;
      }
    }
  }

  // Axis sorting
  function sortCategoryAxis() {
    // Sort by value
    series_bar.dataItems.sort(function (x, y) {
      return y.get("graphics").y() - x.get("graphics").y();
    });

    let easing = am5.ease.out(am5.ease.cubic);

    // Go through each axis item
    am5.array.each(yAxis.dataItems, function (dataItem) {
      // get corresponding series item
      let seriesDataItem = getSeriesItem(dataItem.get("category"));

      if (seriesDataItem) {
        // get index of series data item
        let index = series_bar.dataItems.indexOf(seriesDataItem);

        let column = seriesDataItem.get("graphics");

        // position after sorting
        let fy =
          yRenderer.positionToCoordinate(yAxis_bar.indexToPosition(index)) -
          column.height() / 2;

        // set index to be the same as series data item index
        if (index != dataItem.get("index")) {
          dataItem.set("index", index);

          // current position
          let x = column.x();
          let y = column.y();

          column.set("dy", -(fy - y));
          column.set("dx", x);

          column.animate({ key: "dy", to: 0, duration: 600, easing: easing });
          column.animate({ key: "dx", to: 0, duration: 600, easing: easing });
        } else {
          column.animate({ key: "y", to: fy, duration: 600, easing: easing });
          column.animate({ key: "x", to: 0, duration: 600, easing: easing });
        }
      }
    });

    // Sort axis items by index.
    // This changes the order instantly, but as dx and dy is set and animated,
    // they keep in the same places and then animate to true positions.
    yAxis_bar.dataItems.sort(function (x, y) {
      return x.get("index") - y.get("index");
    });
  }

  // Set data
  let data_bar = [
    {
      country: "USA",
      value: 2025,
    },
    {
      country: "China",
      value: 1882,
    },
    {
      country: "Japan",
      value: 1809,
    },
    {
      country: "Germany",
      value: 1322,
    },
    {
      country: "UK",
      value: 1122,
    },
  ];

  yAxis_bar.data.setAll(data_bar);
  series_bar.data.setAll(data_bar);

  // Make stuff animate on load
  // https://www.amcharts.com/docs/v5/concepts/animations/
  series_bar.appear(1000);
  chart_bar.appear(1000, 100);
});

onUnmounted(() => {
  root.dispose();
  root_bar.dispose();
});
</script>

<style scoped lang="scss">
