}

.box-row {
  display: flex;
  justify-content: space-between;
}
.box {
  padding: 1rem;
  flex: 1;
  background-color: #fafafa;
  border: 1px solid #ededed;
  border-radius: 1rem;
  cursor: pointer;
}

.box-color {
  padding: 1rem;
