<template>
  <div class="login_container">
    <div class="input_form">
      <h6>   .</h6>
      <h6> .</h6>
      <div style="margin-top: 20px">
        :
        <span style="color: red; font-weight: 600">{{ data.maskedId }}</span>
      </div>
      <form @submit.prevent.stop="onSubmit">
        <div style="margin-top: 20px">
          <InputBasic
            style="margin-top: 10px"
            outlined
            placeholder="  ."
            v-model="data.password"
            lazy-rules
            debounce="500"
            type="password"
          />
          <InputBasic
            style="margin-top: 10px"
            outlined
            placeholder="  ."
            v-model="data.passwordConfirm"
            lazy-rules
            debounce="500"
            type="password"
          />
          <div class="fail_to_validate" v-if="checkValidation">
            {{ validText }}
          </div>
          <ButtonBasic
            style="width: 100%; margin-top: 10px"
            title=""
            :clicked="onSubmit"
          />
        </div>
      </form>
    </div>
  </div>
</template>

<script name="Initialize" setup>
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import InputBasic from "@/components/atom/input/InputBasic.vue";
import { reactive, onMounted, ref } from "vue";
import { useRouter } from "vue-router";
import { DialogStore } from "@/store/modules/dialog";
import AUTH_API from "@/api/modules/auth";
import { PASSWORD } from "@/helpers/utils";

const router = useRouter();
const dialog = DialogStore();
const checkValidation = ref(false);
const validText = ref("");
const { changepassword } = AUTH_API();

const data = reactive({
  maskedId: "",
  originalId: history.state.id,
  password: "",
  passwordConfirm: "",
});

onMounted(() => {
  let userid = history.state.id;
  data.maskedId = userid.slice(0, userid.length - 2) + "**";
});

console.log(data);

//    submit  
const onSubmit = async () => {
  if (data.password === "" || data.passwordConfirm === "") {
    checkValidation.value = true;
    validText.value = " .";
    return;
  } else if (data.password !== data.passwordConfirm) {
    dialog.openAlertDialog({
      message: "  .   .",
      buttonText: "",
    });
    checkValidation.value = false;
    return;
  } else if (!PASSWORD.test(data.password)) {
    dialog.openAlertDialog({
      message:
        " 8~20 ,,  .",
      buttonText: "",
    });
    checkValidation.value = false;
    return;
  } else {
    checkValidation.value = false;
    const response = (data, statusCode) => {
      if (statusCode === "823") {
        dialog.openAlertDialog({
          message: "   .   .",
          buttonText: "",
        });
        return;
      } else if (statusCode === "828") {
        dialog.openAlertDialog({
          message: "   .",
          buttonText: "",
        });
        return;
      } else if (statusCode === "200") {
        dialog.openAlertDialog({
          message: " .",
          buttonText: "",
        });
        router.push("/login");
      }
    };
    await changepassword(
      {
        loginId: data.originalId,
        password: data.password,
      },
      response,
    );
  }
};
</script>

<style lang="scss" scoped>
.login_container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, $gray_100 50%, #ffffff 50%);
}

.input_form {
  width: 500px;
  margin-top: 30px;
  background-color: $white;
  box-shadow: 0px 30px 100px rgba(68, 68, 79, 0.13);
  padding: 20px;
}

.fail_to_validate {
  padding-top: 10px;
  font-size: 12px;
  color: $red;
}
</style>
