<template>
  <div class="calendar_container">
    <q-input
      v-bind="attrs"
      square
      outlined
      v-model="date"
      dense
      placeholder="0000-00-00"
      :input-style="{
        color: '#8E8E8E',
      }"
    >
      <template v-slot:append>
        <q-icon
          name="expand_more"
          class="cursor-pointer"
          style="color: #616161"
        >
          <q-popup-proxy
            transition-show="jump-down"
            transition-hide="jump-up"
            :offset="[130, 10]"
          >
            <q-date v-model="date" mask="YYYY-MM-DD" minimal>
              <div class="row items-center justify-end">
                <q-btn v-close-popup label="Close" color="primary" flat />
              </div>
            </q-date>
          </q-popup-proxy>
        </q-icon>
      </template>
    </q-input>
  </div>
</template>

<script setup name="Calendar">
import { defineProps, watch, defineEmits, ref, useAttrs } from "vue";
const attrs = useAttrs();

const emit = defineEmits(["changeDate", { date: String }]);
const props = defineProps({
  date: { type: String, required: true },
});
const date = ref(props.date);

watch(date, (e) => {
  let arg = { date: e };
  emit("changeDate", arg);
});
</script>

<style lang="scss" scoped>
.calendar_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 10px 10px 10px 0;
}

.date {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  width: 200px;
  height: 40px;
  border: 1px solid #ddd;
}

.calendar {
  position: absolute;
  z-index: 999;
  top: 40px;
}
</style>
